import React, { Component } from 'react';
import classes from './SelectCourse.css';
import './SelectCourse.css';
import image from '../../check-icon.png';
import $ from 'jquery';

var styles = {
color:'violet',
backgroundImage: 'url('+image+')'
};
var styles2 ={
  width: '90%',
  margin: 'auto',
  height: '500px',
}

class SelectCourse extends Component {

  constructor(props){
    super(props);
    this.state = {
      items : [],
      course_id: '',
      isLoaded : false,
    }
  }
  handleSelectRound(){
    
    var course_ids = $("input[name='course_check[]']").map(function(){
      if($(this).hasClass('checked')){
         return $(this).val();
       }}).get();
    var round_id = $("input[name='round_id']:checked").val();
    var play_time = $("input[name='play_check']:checked").val();
    fetch('http://codelive.info/pbg_golf/api//create_package_step1_post',{
      method: 'POST',
      headers: {
              //"Content-Type": "application/json",
               "Content-Type": "application/x-www-form-urlencoded",
      },
      body:  "playing_time="+play_time+"&round_id="+round_id,
    })
    .then(res => res.json())
    .then(json => {
      
      sessionStorage.setItem('course_ids', course_ids);
      sessionStorage.setItem('round_id',round_id);
      sessionStorage.setItem('playing_time',play_time);
      sessionStorage.setItem('rc_id',json.data.rc_id);
      
      window.location = '/SelectIncentive';
    });
  }

  componentDidMount(){
    fetch('http://codelive.info/pbg_golf/api/golf_courses_post')
    .then(res => res.json())
    .then(json => {
      this.setState({
        isLoaded : true,
        items : json.data,
      })
    });
    
  }
  handleSelectRoundCombination() {
    var x = $("#course_count").val();
    if(x >0){
        window.$('#myModal2').modal('show');
        $('#myModal2').addClass('in');
    }else{
      alert("please select atleast one course");
    }  
  }
  handleIsCheck(elem){
   let el = elem.target;
   var x = $("#course_count").val();
      if($(el).hasClass('checked') == false){
          $(el).addClass('checked');
          x++;
          $("#course_count").val(x);
    }else{
        $(el).removeClass('checked');
        x--;
          $("#course_count").val(x);
    }
  }
  isEmpty(obj) {
    for(var key in obj) {
        if(obj.hasOwnProperty(key))
            return true;
    }
    
    return false;

  }
  render() {
    
    var { isLoaded, items } = this.state;

    if (!isLoaded) {
      return <div> Loading....</div>;
    }
    else{
          return (
          <div className={classes.SelectCourse}>
              <section className={classes.SliderMain, classes.HeaderImgBanner}>
              <div className={classes.BannerMain}>
                      <div className={classes.BsSliderOverlay}></div>
                      <img src="http://codelive.info/pbg_golf/front_assets/images/slider1.jpg" alt="Slider1" className="slide-image" />
                        <div className="container ">
                    <div className="row">
                      <div className={classes.SlideText}>
                        <h1 data-animation="animated zoomInRight" className="">Search Your Golf Courses</h1>
                        <div className="btn_select animated fadeInLeft" data-animation="animated fadeInLeft">
                          <div className="col-md-8 col-md-offset-2">
                            <form id={classes.SearchCourseForm} autoComplete="off">
                              <input type="hidden" id="start_value" value="" />
                              <div className="input-group" id={classes.SearchBox}>
                                <input type="text" name="search_value" className="form-control search_input" placeholder="Enter Course, City, or Postal Code" id="search_box" />
                                <span className="input-group-addon">
                                  <input type="submit" id={classes.SearchCoursebtn} className="form-control search_btn" value="Search" />
                                </span>
                              </div>
                                        <div id="getAutoComplete">

                                        </div>
                                </form>
                              </div>
                            </div>
                          </div>
                        </div>
                      </div>      
                            </div>
                  
               
              </section>
          <section class="fix">
        <div className={classes.Wizard}>
            <div className={classes.WizardInner}>
                <div className={classes.ConnectingLine}></div>
                <ul className="nav nav-tabs" role="tablist">

                    <li role="presentation" className={classes.ActiveTabs}>
                        <a className={classes.ClickTab} href="javascirpt:void(0);" data-toggle="tab" aria-controls="step1" role="tab" title="" data-original-title="Step 1">
                            <span className={classes.RoundTab}></span>
                        </a>
            <p>Select Course</p>
            
                    </li>
                    <li role="presentation">
                        <a className={classes.ClickTab} href="javascirpt:void(0);" data-toggle="tab" aria-controls="step2" role="tab" title="" data-original-title="Step 2">
                            <span className={classes.RoundTab2}></span>            
                        </a>
            <p>Select Incentive</p>
                    </li>
                    <li role="presentation">
                        <a className={classes.ClickTab} href="javascirpt:void(0);" data-toggle="tab" aria-controls="step3" role="tab" title="" data-original-title="Step 3">
                            <span className={classes.RoundTab2}></span>
                        </a>
            <p>Review</p>
                    </li>

                    <li role="presentation">
                        <a className={classes.ClickTab} href="javascirpt:void(0);" data-toggle="tab" aria-controls="complete" role="tab" title="" data-original-title="Complete">
                            <span className={classes.RoundTab2}></span>
                        </a>
            <p>Payment</p>
                    </li>
                </ul>
            </div>
  <form role="form" id="addForm">
         <div class="tab-content">
        
          
          
          
    <div class="tab-pane active in" role="tabpanel" id="step1">
          
          <ul className={classes.Block}>
                        <li className={classes.PullRight}><button type="button" onClick={this.handleSelectRoundCombination} className="btn btn-primary btn-lg">Next Step</button></li>
                      </ul>
          
          
          <div class="process-filed">
            <div class="container">
            <div class="row" id="AllCourses"> <input type="hidden" value="0" name="course_count" id="course_count" />
  <input type="hidden" value="0" name="round_count" id="round_count" />
   {this.isEmpty(items)?items.map(item =>(
     <div class="col-md-6 col-sm-12 col-xs-12">                  
 <div className={classes.ProcesList}>
    <div className={classes.ProcessHeading}>
     <h2>{item.course_name}<a href="http://codelive.info/pbg_golf/course/1" className={classes.ProButton}>829 mi</a></h2>
    </div>
     <div className={classes.CheckboxFile}>
      <input type="checkbox" name="course_check[]" style={styles} onClick={((e) => this.handleIsCheck(e))} value={item.id} class="course_chx_1 courses" />
      <input type="hidden" name="course_id[]" class="course_id" value={item.id} />
     </div>
 </div>
</div>
    )):(<div className="col-md-12"><div className="col-md-6">No Data Found</div></div>)};       

 <input type="hidden" name="gc_id" id="g_id" value="" />   

            </div>
            </div>
          </div>
          
        </div>
        
      </div>
          <div className="modal fade new_modal Modal12" id="myModal2" role="dialog" style={styles2}>
          <div className="modal-dialog">
          
            
            <div className="modal-content">
            
            <div className="modal-body">
              <div className="process-filed">
                      <div class="container">
                        <h3 class="text-center">Playing Time</h3>
                        <div class="playing_time">


                          <div class="col-md-4 col-sm-4 col-xs-12 pad0">
                          <div className={classes.ProcessList, classes.ProjectList}>
                              <div className={classes.ProcessHeading}>
                               <h4>Weekday</h4>
                              </div>
                               <div className={classes.CheckboxFile}>
                                <input type="radio" name="play_check" value="3" class="play_check form-control"  checked/>
                               </div>
                           </div>
                          </div>
                          <div class="col-md-4 col-sm-4 col-xs-12 pad0">
                          <div className={classes.ProcessList, classes.ProjectList}>
                              <div className={classes.ProcessHeading}>
                               <h4>Weekend</h4>
                              </div>
                               <div className={classes.CheckboxFile}>
                                <input type="radio" name="play_check" value="4" class="play_check form-control" />
                               </div>
                           </div>
                          </div>                          
                          <div class="col-md-4 col-sm-4 col-xs-12 pad0">
                         <div className={classes.ProcessList, classes.ProjectList}>
                             <div className={classes.ProcessHeading}>
                               <h4>Both</h4>
                              </div>
                              <div className={classes.CheckboxFile}>
                                <input type="radio" name="play_check" value="5" class="play_check form-control" />
                               </div>
                           </div>
                          </div>
                        </div>
                        <div class="clearfix"></div>
                        <div className={classes.MainDiv, classes.Rounds}>
                        <h3 class="text-center">Rounds</h3>
                          <div class="col-md-6 col-sm-6 col-xs-12 pad0">
                          <div className={classes.ProcessList, classes.ProjectList}>
                              <div className={classes.ProcessHeading}>
                               <h2>9 &amp; 18 Hole Options </h2>
                              </div>
                               <div className={classes.CheckboxFile}>
                              
                              <input type="radio" name="round_id" value="1" class="round_check form-control" checked/>
                               </div>
                           </div>
                          </div>                
                          <div class="col-md-6 col-sm-6 col-xs-12 pad0">
                          <div className={classes.ProcessList, classes.ProjectList}>
                              <div className={classes.ProcessHeading}>
                               <h2>18 Holes Only </h2>
                              </div>
                              <div className={classes.CheckboxFile}>
                              <input type="radio" name="round_id" value="2" class="round_check form-control" />

                               </div>
                           </div>
                          </div>                
                        </div>
                      </div>
                    </div>
            </div>
            <div class="modal-footer ">
              <div class="text-center">
              <button type="button" onClick={this.handleSelectRound} className={classes.BtnDefault}>Submit</button>
              <button type="button" className={classes.CloseBtn} data-dismiss="modal">Close</button>
              </div>
            </div>
            </div>
          </div>
          </div>      
      
      </form>

        
        </div>
    </section>
     </div>



        );
    }
  }
}

export default SelectCourse;
