import React, { Component } from 'react';
import classes from './ResetPassword.css';
import Home from '../Home/Home';
import OTP from '../OTP/OTP';
import image from '../../bg.jpg';
import $ from 'jquery';
import Notifications, {notify} from 'react-notify-toast';
import {
  BrowserRouter as Router,
  Route,
  Link,
  Redirect,
  withRouter
} from "react-router-dom";

var styles = {
color:'violet',
backgroundImage: 'url('+image+')'
};

class ResetPassword extends Component {

  constructor(props){
    super(props);
    this.state = {
      verif_pass: this.props.match.params.forgot_user_id,
      password: '',
      cpassword: '',
      isLoaded : false,
    }
  }

  
  handleSubmit(event){
    event.preventDefault();
    const data = new FormData(event.target);
   fetch('http://codelive.info/pbg_golf/api/reset_password', {
      method: 'POST',
      body: data
    })
   .then(res => res.json())
    .then(json => {
      $(".list-unstyled").html("");
      if(json.replyStatus === 'success'){
        sessionStorage.setItem('userId', json.data.id);
        sessionStorage.setItem('userName', json.data.fname+" "+json.data.lname);
        sessionStorage.setItem('isLogin',true);
        
        window.location = '/Profile';
        notify.show(json.replyMsg,"success", 5000, "green");
    }else if(json.replyStatus === 'otp_auth'){
          window.location = '/OTP';
        }
      else{
          var error = JSON.parse(json.replyMsg);
          $.each(error, function(k, v) { 
          var er = v; 
          $(".list-unstyled").append("<li>"+er+"</li>");
          $("#new_error_div").removeClass('hide');
          $("#sub_button").html('Login'); 
        });
        }
     })
    .catch((error)=>{
      notify.show(error,"error", 5000, "red");
    })
  }
  render() {
    
    var { isLoaded, items } = this.state;

    if (!isLoaded) {
      return(
      <div className={classes.ResetPassword} style={styles}>
      <Notifications options={{zIndex: 200, top: '100px'}} />
      <div className={classes.LoginBox}>
  <div className={classes.LoginBoxBody, classes.Sty1}>
  <div className={classes.LoginLogo}>
      <a href="http://codelive.info/pbg_golf/"><img src="http://codelive.info/pbg_golf/admin_assets/img/logo/1539007624.png" alt="" className={classes.ImgWidth}/></a>
  </div>
  
    <p className={classes.LoginBoxMsg}>Reset Password</p>
  <div className={classes.ErrorHolder}>
    <div id={classes.NewErrorDiv} className={classes.List3, classes.Hide}>
      <h6>Instructions</h6>
      <ul className="list-unstyled">
        
      </ul>
    </div>
  </div>
  
  <div className="clearfix"></div>
    <form id="resetPassForm" onSubmit={this.handleSubmit}>
      
    
    <div className="input-group mb-3 has-feedback">
      
      <input type="password" name="password" id="password" className="form-control sty1" placeholder="New Password" aria-label="Recipient's username" aria-describedby="basic-addon2" />
      <div className="input-group-append">
      <span className="input-group-text" id="basic-addon2" data-toggle="tooltip" data-placement="top" title="" data-original-title="Tooltip on top"><i className="fa fa-exclamation-circle"></i></span>
      </div>
    </div>
    <div className="row">
    <div className="col-md-3">
      <p>Strength:</p>
    </div>
    <div className="col-lg-9">
      <div className="progress skill-bar progress_bar_strength">
        <div className="progress-bar " role="progressbar" aria-valuemin="0" aria-valuemax="100">
          <span className="skill"></span>
        </div>
      </div>
    </div>  
    </div>  
    
      <div className="form-group has-feedback">
        <input type="password" name="cnf_pass" className="form-control sty1" placeholder="Confirm New Password" />
        <input type="hidden" name="verif_pass" value={this.state.verif_pass} />
      </div>
      <div>
        <div className="col-xs-12 m-t-1">
          <button type="submit" id="sub_btn" className="btn btn-primary btn-block btn-flat">Submit</button>
        </div>
    
    
    
    
       
      </div>
    </form>
  </div>
 
</div>
</div>
    );
    }
    else{
          return (
           <div>hello</div>
        );
    }
  }
}

export default withRouter(ResetPassword);
