import React, { Component } from 'react';
import classes from './Cart.css';
import {
  BrowserRouter as Router,
  Route,
  Link,
  Redirect,
  withRouter
} from "react-router-dom";
var displayNone = {
	display: 'none' 
};
var AdminMenuBack = {
	backgroundColor: '#0075B8'
}
var DashboardHeight={
	minHeight: '400px'
}
var button = {
  display: 'inline-block',
    width: '100px', 
    fontSize: '12px',
    padding: '8px 10px',
    marginRight: '5px',
    borderRadius: '3px',
} 
var sideBar ={
  borderRadius: '0',
    borderBottom: '1px solid #ffffff70',
    lineHeight: '30px',
}

class Cart extends Component {
  constructor(props){
    super(props);
    this.state = {
      isLoaded : false,
      items: false
    }
  }

  isEmpty(obj) {
    for(var key in obj) {
        if(obj.hasOwnProperty(key))
            return true;
    }
    
    return false;

  }
  componentDidMount(){
  	var user_id = sessionStorage.getItem('userId');
	fetch('http://codelive.info/pbg_golf/api/golfer_cart_data_post', {
        method: 'POST',
      	headers: {
              //"Content-Type": "application/json",
               "Content-Type": "application/x-www-form-urlencoded",
      	},
      	body: "user_id="+user_id,
    })
    .then(res => res.json())
    .then(json => {
    	this.setState({
        isLoaded : true,
        items: json.data
      })
  	});
  }


deleteSingleItem(e,cart_id){

	var user_id = sessionStorage.getItem('userId');
	fetch('http://codelive.info/pbg_golf/api/remove_item_from_cart_post', {
        method: 'POST',
      	headers: {
              //"Content-Type": "application/json",
               "Content-Type": "application/x-www-form-urlencoded",
      	},
      	body: "user_id="+user_id+"&cart_id="+cart_id,
    })
    .then(res => res.json())
    .then(json => {
    	if(json.replyStatus ==="success"){
    		window.location='/Cart';
    	}
  	});
}

deleteAllItem(){
	var user_id = sessionStorage.getItem('userId');
	fetch('http://codelive.info/pbg_golf/api/remove_all_from_cart_post', {
        method: 'POST',
      	headers: {
              //"Content-Type": "application/json",
               "Content-Type": "application/x-www-form-urlencoded",
      	},
      	body: "user_id="+user_id,
    })
    .then(res => res.json())
    .then(json => {
    	if(json.replyStatus ==="success"){
    		window.location='/Cart';
    	}
      
    	
  	});
}

   goToProfile(){
  	window.location = '/Profile';
  }
   goToOrder(){
  	window.location = '/Order';
  }
   goToDashboard(){
  	window.location = '/Dashboard';
  }


  render(){
	if(!sessionStorage.getItem('isLogin')){ window.location = '/Login' }
	var { isLoaded, items } = this.state;

    if (!isLoaded) {
      return <div> Loading....</div>;
    }
    else{
	return(
			<div className={classes.Cart}>
				<section className={classes.AboutMidSection}>
					<Router>
					<div>
					<div className="container-fluid">
    					<div className="row">
        					<div className="col-sm-2">
		            			<ul className="nav nav-pills nav-stacked " style={AdminMenuBack}>
                					<li onClick={this.goToDashboard}><Link to="/Dashboard" style={sideBar}><i className="fas fa-tachometer-alt fa-fw"></i> Dashboard</Link></li>
					                <li onClick={this.goToProfile}><Link to="/Profile" style={sideBar}><i className="fa fa-user-circle fa-fw"></i> Profile</Link></li>
					                {/*<li><Link to="/Reports" style={sideBar}><i className="fas fa-chart-bar fa-fw"></i> Reports</Link></li>
					                <li><Link to="/Settings" style={sideBar}><i className="fa fa-cog fa-fw"></i> Settings</Link></li>*/}
					                <li onClick={this.goToOrder}><Link to="/Orders" style={sideBar}><i className="fab fa-first-order fa-fw"></i> Orders</Link></li>
					                <li exact className={classes.Active}><Link to="/Cart" style={sideBar}><i className="fa fa-shopping-cart fa-fw"></i> Cart</Link></li>
            					</ul>
            				</div>
					      	<div className="col-sm-10">
					<div classNmae="col-sm-12"><h2 style={{marginTop: '0px'}}>Your Cart <a className="btn btn-danger btn_small text-center pull-right" href="javascript:void(0);" onClick={this.deleteAllItem} style={button}><i className="fa fa-trash"></i> Remove All </a></h2></div>

					{this.isEmpty(items)?items.golfer_cart_data.map(item =>(
                
		                  <div className="col-sm-4">
								<div className={classes.BoxShowd}>
									 <div className="col-sm-12 ptb10">
										
										<div className="col-sm-12 ">
										   <h5 className="bold">{items.golfer_cart_data[0].courses}</h5>
										   <p className={classes.FontXs}>
                        <b>Incentives:-</b>
                         {items.golfer_cart_data[0].incentive_names}<br />
                        <b>Total Incentive Cost:- $500</b><br />
                        <b>Ancillary Coupon:-</b><br />
                        <div dangerouslySetInnerHTML={{__html:items.golfer_cart_data[0].ancillary_item}} />
                        <b>Total Ancillary Amount:-</b>
                         ${items.golfer_cart_data[0].ancillary_total_amt}<br/>
                         </p>
										   
										   <h4 className={classes.PriceText}> Total Cost:- <i class="fa fa-usd"></i>{item.total_amt}</h4>
										</div>
									 </div>
									 <div className=" col-sm-12"><a href="http://codelive.info/pbg_golf/review/Wmg4T2FZTkNhVjFIMGlrU1ZDUkhMZz09" type="button" className={classes.BtnInfo}> Review </a>
										<a href="http://codelive.info/pbg_golf/place-order/Wmg4T2FZTkNhVjFIMGlrU1ZDUkhMZz09" type="button" className={classes.BtnSuccess}> Order Now </a> <button type="button" onClick={((e) => this.deleteSingleItem(e,item.golfer_inc_cart_id))} className={classes.BtnDanger}>Remove </button>
									 </div>
								</div>
							</div>

		              )):(
					<div class="col-sm-12">
				<hr/>
				<div class="alert alert-danger fade in alert-dismissible">
					
					No Item Available in Your Cart !! To Create Package  <a href="javascript:void(0);">Click Here</a>.
				</div>
				
				</div>
				)} 

	            </div>
						</div>
					</div>
</div>
</Router>
</section>
			</div>
		);
}
}
};



export default withRouter(Cart);
