import React, { Component } from 'react';
import Slider from '../../components/Slider/Slider';
import ProcessSection from '../../components/ProcessSection/ProcessSection';
import Subscribe from '../../components/Subscribe/Subscribe';
import Footer from '../../components/Footer/Footer';
import classes from './Home.css';
import $ from 'jquery';

var SearchBox = {
    height: '50px',
    backgroundColor: '#f7f7f7',
    color: '#333 !important',
    fontSize: '16px',
    boxShadow: 'none',
    border: '1px solid #e0e0e0',
    borderTopLeftRadius: '3px',
    borderBottomLeftRadius: '3px',
}
class Home extends Component {

  constructor(props){
    super(props);
    this.state = {
      start: '',
      search_value: '',
      latitude: '',
      langitude : '',
      items : [],
    }
    this.getMyLocation = this.getMyLocation.bind(this)


  }
  
  handleSearch(event){

    //this.getMyLocation()
  event.preventDefault();
    const data = new FormData(event.target);
    console.log(data);
   fetch('http://codelive.info/pbg_golf/api/get_golf_courses_post', {
      method: 'POST',
      body: data
  
    })

   .then(res => res.json())
    .then(json => {

      
      if(json.replyStatus === 'success'){

        window.location = '/SelectCourse';
    }else if(json.replyStatus === 'error'){
          window.location = '/OTP';
        }
      else{
          alert(json.replyStatus);
        }
     })
    .catch((error)=>{
      console.error(error);
    })
}

 getMyLocation() {
    //const location = window.navigator && window.navigator.geolocation
    const location = window.navigator.geolocation
    
    if (location) {
      location.getCurrentPosition((position) => {
        this.setState({
          latitude: position.coords.latitude,
          longitude: position.coords.longitude,
        })
      }, (error) => {
        this.setState({ latitude: 'err-latitude', longitude: 'err-longitude' })
      })
    }
  }

  isEmpty(obj) {
    for(var key in obj) {
        if(obj.hasOwnProperty(key))
            return true;
    }
    
    return false;

  }

  handleAutoComplete(e){
    fetch('http://codelive.info/pbg_golf/api/auto_complete_data', {
      method: 'POST',
      headers: {
              //"Content-Type": "application/json",
               "Content-Type": "application/x-www-form-urlencoded",
      },
        body:  "search_value="+e.target.value,
    })

   .then(res => res.json())
    .then(json => {
      if(json.replyStatus === 'success'){
        $(".auto_c_class").removeClass("hide");
       this.setState({
        isLoaded : true,
        items : json.data,
      })
    }else if(json.replyStatus === 'error'){
          alert(json.data);
        }
      else{
          alert(json.replyStatus);
        }
     })
    .catch((error)=>{
      console.error(error);
    })
  }

  handleSearchAuto(e,name){
    $('#search_box').val(name);
  $('#searchCourseForm').submit()
  }

  render() {
    this.getMyLocation();
    var { isLoaded, items } = this.state;
    return (
      <div className={classes.Home}>
        <div className="container">
          <div className="row">
            <div className="btn_select paddTB_25" data-animation="animated fadeInLeft">
              <div className="col-md-7 col-sm-12 col-xs-12">
               <form id="searchCourseForm" method="GET" onSubmit={this.handleSearch} autoComplete="on">
                <input type="hidden" id="start_value" value="" />
                  <div className="input-group" id={classes.InputGroup}>
                <input type="text" name="search" className="form-control search_input" placeholder="Enter Course, City, or Postal Code" id="search_box" style={SearchBox} onChange={((e) => this.handleAutoComplete(e))} autoComplete="off"/>
                <input type="hidden" name="latitude" value={this.state.latitude}/>
                <input type="hidden" name="longitude" value={this.state.longitude} />
                <input type="hidden" name="start" value="0" />
                <span className="input-group-addon">
              <input type="submit" id={classes.SearchCoursebtn} className="form-control search_btn" value="Search" />
            </span>
          </div>
          <div id="getAutoComplete" className={classes.GetAutoComplete}>
          <ul className="auto_c_class hide">
          {this.isEmpty(items.autocomplete_data)?items.autocomplete_data.map(auth =>(
            <li onClick={((e) => this.handleSearchAuto(this,auth.data))}>{auth.data}</li>
          )):''}  
          </ul> 
          </div>
        </form>
      </div>
    </div>
          </div>
        </div>
        <Slider />
        <ProcessSection />
      </div>
    );
  }
}

export default Home;
