import React, { Component } from 'react';
import classes from './Terms.css';

class Terms extends Component {

  constructor(props){
    super(props);
    this.state = {
      items : [],
      isLoaded : false,
    }
  }

  componentDidMount(){
    fetch('http://codelive.info/pbg_golf/api/terms_post',
     {headers: {
                'Access-Control-Allow-Origin': '*',
                'Content-Type': 'application/json',
                'Accept': 'application/json',                  
            }
          })
    .then(res => res.json())
    .then(json => {
      this.setState({
        isLoaded : true,
        items : json,
      })
    });
  }
  render() {
    
    var { isLoaded, items } = this.state;

    if (!isLoaded) {
      return <div> Loading....</div>;
    }
    else{
          return (
          <div className={classes.Terms}>
          <div dangerouslySetInnerHTML={{__html:items.data.content}} />
    </div>
        );
    }
  }
}

export default Terms;
