import React, { Component } from 'react';
import classes from './ForgetPassword.css';
import Home from '../Home/Home';
import OTP from '../OTP/OTP';
import image from '../../bg.jpg';
import $ from 'jquery';
import Notifications, {notify} from 'react-notify-toast';
import {
  BrowserRouter as Router,
  Route,
  Link,
  Redirect,
  withRouter
} from "react-router-dom";

var styles = {
color:'violet',
backgroundImage: 'url('+image+')'
};
var mt1={
marginTop: '10px'
}

class ForgetPassword extends Component {

  constructor(props){
    super(props);
    this.state = {
      email: '',
      isLoaded : false,
    }
  }

  
  handleSubmit(event){
    event.preventDefault();
    const data = new FormData(event.target);
   fetch('http://codelive.info/pbg_golf/api/forgot_password_post', {
      method: 'POST',
      body: data
    })
   .then(res => res.json())
    .then(json => {
      $(".list-unstyled").html("");
      if(json.replyStatus === 'success'){
        notify.show(json.replyMsg,"success", 5000, "green");
    }
      else{
          var error = JSON.parse(json.replyMsg);
          $.each(error, function(k, v) { 
          var er = v; 
          $(".list-unstyled").append("<li>"+er+"</li>");
          $("#new_error_div").removeClass('hide');
          $("#sub_button").html('Login'); 
        });
        
        }
     })
    .catch((error)=>{
      notify.show(error,"error", 5000, "red");
    })
  }
  render() {
    
    var { isLoaded, items } = this.state;

    if (!isLoaded) {
      return(
      <div className={classes.ForgetPassword} style={styles}>
        <Notifications options={{zIndex: 200, top: '100px'}} />
      <div className={classes.LoginBox}>
  <div className={classes.LoginBoxBody}>
    <h3 className={classes.LoginBoxMsg}>Forgot Password</h3>
  <div className={classes.ErrorHolder}>
    <div id={classes.NewErrorDiv} className={classes.List3, classes.Hide}>
      <h6>Instructions</h6>
      <ul className={classes.ListUnstyled}>
        
      </ul>
    </div>
  </div>
    <p>Enter your Email and instructions will be sent to you! </p>
  
    <form id="forgotPassForm" onSubmit={this.handleSubmit}>
      
      <div className="form-group has-feedback">
        <input type="email" name="email" className="form-control sty1" placeholder="Email" />
      </div>
      <div>
        <div className="col-xs-12" style={mt1}>
          <button type="submit" id="sub_btn" class="btn btn-primary btn-block btn-flat">Reset</button>
        </div>
    <div class="col-xs-12 " style={mt1}>
     <a className="btn btn-info btn-block" href="http://codelive.info/pbg_golf/user_login">Back</a> 
    </div>
      </div>
    </form>
  </div>
  
</div>
</div>
    );
    }
    else{
          return (
           <div>hello</div>
        );
    }
  }
}

export default withRouter(ForgetPassword);
