import React, { Component } from 'react';
import classes from './Review.css';
import image from '../../bg.jpg';
import $ from 'jquery';

var styles = {
color:'violet',
backgroundImage: 'url('+image+')'
};

var modal ={
      width: '600px !important',
    margin: '30px auto !important',
    height: 'auto !important',
}

class Review extends Component {

  constructor(props){
    super(props);
    this.state = {
      items : [],
      course_id: '',
      isLoaded : false,
    }
  }


  componentDidMount(){
    
    var cart_id = sessionStorage.getItem('cart_id');
    var user_id = sessionStorage.getItem('userId');
    fetch('http://codelive.info/pbg_golf/api/cart_review_post',{
      method: 'POST',
      headers: {
              //"Content-Type": "application/json",
               "Content-Type": "application/x-www-form-urlencoded",
      },
      body:  "cart_id="+cart_id+"&cart_hash_id=&user_id="+user_id,
    })
    .then(res => res.json())
    .then(json => {
      if(json.replyStatus ==="success"){
      this.setState({
        isLoaded : true,
        items : json.data,
      })
    }else{
      window.location = '/SelectCourse';
    }
    });

  }

  handleMoreDetails(){
    window.$('#myModal').modal('show');
        $('#myModal').addClass('in');
  }
  addNewPackage(){
      sessionStorage.removeItem('course_ids');
      sessionStorage.removeItem('round_id');
      sessionStorage.removeItem('playing_time');
      sessionStorage.removeItem('rc_id');
      sessionStorage.removeItem('cart_id');
      sessionStorage.removeItem('inc_cart_id');
      window.location = '/SelectCourse';
        }
  addMoreData(){
      sessionStorage.removeItem('inc_cart_id');
      window.location = '/SelectIncentive';
  }
  handleOrderNow(){
    window.location = '/PlaceOrder';
  }

  render() {
    
    var { isLoaded, items } = this.state;

    if (!isLoaded) {
      return <div> Loading....</div>;
    }
    else{
          return (
          <div className={classes.Review}>
        <section class="fix">
        <div className={classes.Wizard}>
          <div className={classes.WizardInner}>
                <div className={classes.ConnectingLine}></div>
                <ul className="nav nav-tabs" role="tablist">

                    <li role="presentation" className={classes.ActiveTabs}>
                        <a className={classes.ClickTab} href="javascirpt:void(0);" data-toggle="tab" aria-controls="step1" role="tab" title="" data-original-title="Step 1">
                            <span className={classes.RoundTab}></span>
                        </a>
            <p>Select Course</p>
            
                    </li>
                    <li role="presentation" className={classes.ActiveTabs}>
                        <a className={classes.ClickTab} href="javascirpt:void(0);" data-toggle="tab" aria-controls="step2" role="tab" title="" data-original-title="Step 2">
                            <span className={classes.RoundTab}></span>            
                        </a>
            <p>Select Incentive</p>
                    </li>
                    <li role="presentation" className={classes.ActiveTabs}>
                        <a className={classes.ClickTab} href="javascirpt:void(0);" data-toggle="tab" aria-controls="step3" role="tab" title="" data-original-title="Step 3">
                            <span className={classes.RoundTab}></span>
                        </a>
            <p>Review</p>
                    </li>

                    <li role="presentation">
                        <a className={classes.ClickTab} href="javascirpt:void(0);" data-toggle="tab" aria-controls="complete" role="tab" title="" data-original-title="Complete">
                            <span className={classes.RoundTab2}></span>
                        </a>
            <p>Payment</p>
                    </li>
                </ul>
            </div>
            </div>
            </section>
            <form role="form" id="addForm">
      <div class="tab-content">
        
          
          
              <div class="tab-pane active in" role="tabpanel" id="step3">
        
          <ul class="list-inline">
            
            
          </ul>
          
          
          <div className={classes.ProcessFiled}>
            <div class="container">
            <div className={classes.FormReview} id="form_review">
                        
    <div className={classes.FormOver}>
      <div className={classes.GreenFees}>Green Fees</div>
      <div className={classes.OrderNow}>
        <a type="button" href="javascript:void(0);" onClick={((e) => this.addNewPackage())} className={classes.BtnPackageSmall} >Add New Package</a>
        <a type="button" href="javascript:void(0);" onClick={((e) => this.addMoreData())} className={classes.BtnPackageSmall}>Add More Data</a>
        <button type="button" onClick={((e) => this.handleOrderNow())} className={classes.BtnPackageSmall2}>Order Now</button>
        </div>
      <div class="clearfix"></div>
        <div class="form-group form_page">
        
          <input disabled="" type="text" class="form-control" value={items.course_name} />
        </div>
                <div class="form-group form_page fix">
          <div class="col-sm-7 padL0 padR0">
            <input disabled="" type="text" class="form-control" value="Round" />
          </div>  
          <div class="col-sm-2 padL0 padR0 text-center">
            
            <input disabled="" type="text" class="form-control text-right" value={items.calculation_data.retail_ancilary} />
          </div>
          <div class="col-sm-2 padL0 padR0 text-center">
        <input disabled="" type="text" class="form-control text-right" value={items.calculation_data.retail_round_amount} />
        
          </div>
        </div>  
                <div class="form-group form_page fix">
          Gift
          <div class="form-group form_page fix">
                     
            {items.inc_cart_data.map(key =>(
              <div>
              <div class="col-sm-8 padL0 padR0">
                <input disabled="" type="text" class="form-control" value={key.title} />
              </div>  
              <div class="col-sm-3 padL0 padR0">
                <input disabled="" type="text" class="form-control text-right" value={key.item_amount} />
              </div>
              {items.inc_cart_data.length>1?<div class="col-sm-1 padL0 padR0 marT5">
                <a href="javascript:void(0);" class="pull-right btn btn-danger btn-xs" onClick={((e) => this.removeIncentive(items.old_cart_id!=''?items.old_cart_id:items.cart_row.id,key.id))}><i class="fa fa-remove"></i></a>
              </div>:''} 
               </div> 
              ))} 
             </div>
          <div class="form-group form_page fix">
            <div class="col-sm-8 padL0 padR0">
              <input disabled="" type="text" class="form-control" value="Ancillary Coupons" />
            </div>  
            <div class="col-sm-3 padL0 padR0">
              <input disabled="" type="text" id="anci_item_total" class="form-control text-right" value={items.ancilary_total_amt} />
            </div>
          </div>
          <div class="form-group form_page fix">
            <div class="col-sm-8 padL0 padR0">
              <input disabled="" type="text" class="form-control" value="Total Value " />
            </div>  
            <div class="col-sm-3 padL0 padR0">
              <input disabled="" type="text" id="total_retail_amount" class="form-control text-right" value={items.total_retail} />
            </div>
          </div>
          <div class="form-group form_page fix">
            <div class="col-sm-8 padL0 padR0">
              <input disabled="" type="text" class="form-control" value="Your Customize Golf Package" />
            </div>  
            <div class="col-sm-3 padL0 padR0">
              <input disabled="" type="text" id="total_your_amount" class="form-control text-right" value={items.total_amount_paid} />
            </div>
          </div>
        </div>
        <div class="form-group col-sm-11 fix">
          <button type="button" className={classes.BtnPrimary} onClick={this.handleMoreDetails}>More</button>
        </div>
      <div class="clearfix"></div>
       </div>
            </div>
            </div>
          </div>            
        </div>
        
        <div class="clearfix"></div>
      </div>
      </form>
      <div class="modal fade new_modal in" id="myModal" role="dialog">
    <div class="modal-dialog" style={modal}>
    
      {/*<!-- Modal content-->*/}
      <div class="modal-content">
 
        <div class="modal-body">
      <div class="data_tbl table-responsive">
        <table class="table">
        <input type="hidden" value="24" id="cart_id" />
                      <tbody><tr>
              <th>Spring Valley</th>
              </tr> 
                    <tr>
            <td>18 Hole Weekdays Prime time</td>
            <td>2 round</td>
            
            <td>$40</td>
            <td>$80</td>
          </tr>
                  <tr>
            <td>18 Hole Weekdays Non-Prime time</td>
            <td>13 round</td>
            
            <td>$33</td>
            <td>$429</td>
          </tr>
                  <tr>
            <td>9 Holes Weekdays</td>
            <td>18 round</td>
            
            <td>$21</td>
            <td>$378</td>
          </tr>
                  <tr>
            <th>Total</th>
            <th>33 rounds</th>
            
            <td></td>
            <th>$887</th>
          </tr>
        <tr>
          <th>Gift</th>
        </tr>
                <tr>
          <td>M3 Driver</td>
          <td>  Retail  </td>
          <td></td>
          <td> $500</td>
          
        </tr> 
                <tr>
          <th>Total </th>
          <td>  Retail  </td>
          <td></td>         
          <th>$500</th>
          <input type="hidden" id="gift_total_retail_amt" value="500" />
        </tr> 
        <tr>
          <th>Ancillary Coupons</th>
          <td></td>
          <td></td>
        </tr>
                      <tr>
            <td><b>33</b> 2 For  1 Meals&nbsp;(up &nbsp;to $10) W/2 Large Soft Drinks </td>
            <td>value</td>
            <td></td>         
            <td> $330</td>
          </tr>
                      <tr>
            <td><b>33</b> Buy 2 Beers Get Third Free (up To $5) </td>
            <td>value</td>
            <td></td>         
            <td> $165</td>
          </tr>
                      <tr>
            <td><b>33</b> $2 Off Hotdog &amp; Chips </td>
            <td>value</td>
            <td></td>         
            <td> $66</td>
          </tr>
                      <tr>
            <td><b>33</b> $2 Off Bucket Of Balls  </td>
            <td>value</td>
            <td></td>         
            <td> $66</td>
          </tr>
                      <tr>
            <td><b>33</b> $1 Off Any Size Soft Drink  </td>
            <td>value</td>
            <td></td>         
            <td> $33</td>
          </tr>
                
        
        <tr>
          <th>Total</th>
          <th> </th>
          <th></th>
          <th>$660</th>
          <input type="hidden" id="ancilary_total_amt" value="660" />
        </tr> 
        <tr>
          <th>Package Value</th>
        </tr>
        <tr>
          <td>Green fees    </td>
          <td></td>
          <td></td>
          <td> $887</td>
        
        </tr> 
        <tr>
          <td>Gift  </td>
          <td></td>
          <td></td>
          <td>$500</td>
        </tr> 
        <tr>
          <td>Ancillary Coupons</td>
          <td></td>
          <td></td>
          <td>$660</td>
        </tr> 
        <input type="hidden" name="total_paid_amt" id="total_paid_amt" value="2707" /> 
        <tr>
          <th>Total</th>
          <th></th>
          <th></th>
          <th>$2047</th>
            <input type="hidden" id="total_retail" value="2047" />

        </tr>
        
        <tr>
          <th>Your Customize Golf Package</th>
          <th></th>
          <th></th>
          <th>$870</th>
            <input type="hidden" id="total_amt" value="870" />
        </tr>
                
        </tbody></table>
      </div>
        </div>
        <div class="modal-footer">
          <div class="pull-left">
      <button type="button" className={classes.BtnDefault} data-dismiss="modal" onClick={((e) => this.handleOrderNow())}>Order Now</button>
      <button type="button" className={classes.BtnDefault2} data-dismiss="modal">Close</button>
      </div>
        </div>
      </div>
      
    </div>
  </div>
          </div>
        );
    }
  }
}

export default Review;
