import React, { Component } from 'react';
import classes from './Incentives.css';
import $ from 'jquery';
import { BrowserRouter as Router, Route, Link } from "react-router-dom";

var maxHeight ={
  maxHeight: '200px',
    minHeight: '200px',
}

class Incentives extends Component {

  constructor(props){
    super(props);
    this.state = {
      items : [],
      incentive_id: '',
      start:'',
      golf_incentive_id:'',
      manufacturer_id:'',
      isLoaded : false,
    }
    this.handleFilter = this.handleFilter.bind(this);
  }
  handleFilter(event){
    event.preventDefault();
    const data = new FormData(event.target);
    fetch('http://codelive.info/pbg_golf/api/get_golf_incentives_post', {
        method: 'POST',
        body: data
  
    })
    .then(res => res.json())
    .then(json => {
        
      this.setState({
        isLoaded : true,
        items : json.data.golf_incentives,
      })
    });
  }
  isEmpty(obj) {
    for(var key in obj) {
        if(obj.hasOwnProperty(key))
            return true;
    }
    
    return false;

  }
  componentDidMount(){
    fetch('http://codelive.info/pbg_golf/api/golf_incentives_post')
    .then(res => res.json())
    .then(json => {
      this.setState({
        isLoaded : true,
        items : json.data,
      })
    });
  }
  render() {
    
    var { isLoaded, items } = this.state;

    if (!isLoaded) {
      return <div> Loading....</div>;
    }
    else{
          return (
             <div className={classes.Incentives}>
          <section className={classes.SliderMain, classes.HeaderImgBanner}>
                  <div className={classes.BannerMain}>
                   <div className={classes.BsSliderOverlay}></div>
                  <img src="http://codelive.info/pbg_golf/front_assets/images/slider1.jpg" alt="Slider1" className="slide-image" />
                    <div className="container">
                <div className="row">
                  <div className={classes.SlideText}>
                    <h1 data-animation="animated zoomInRight">Incentives</h1>
                  </div>
                </div>
                </div>  
              </div>
          </section>
          <section className="golf_incentives fix padTB50">
                <div className="container">
                  <div className="row">
                    <div className="col-lg-12"><h3 className="text-black">Search by Category &amp; Manufacturer</h3></div>    
                  </div>
                  <form id="incenitve_search" onSubmit={this.handleFilter}>
                  <div className="row padTB10">
                  <div className="col-lg-8 col-xs-12">
                    <div className="col-lg-6">
                      <div className="form-group">    
                          <div className="input-group popup">
                          <div className="input-group-addon" className={classes.Labledv}><label for="golf_incentive_id">Category</label></div>
                            <input className="hidden" id="start_value" value="" />
                            <input className="hidden" name="start" id="start" value="0" />
                            <select name="golf_incentive_id" id="golf_incentive_id" className="js-example-basic-single form-control select2-hidden-accessible" tabIndex="-1" aria-hidden="true">
                                <option value="">--Select--</option>
                                                <option value="1">Drivers</option>
                                                <option value="2">Fairways</option>
                                                <option value="3">Rescues</option>
                                                <option value="4">Irons</option>
                                                <option value="5">Wedges</option>
                                                <option value="6">Putters</option>
                                            </select><span className="select2 select2-container select2-container--default" dir="ltr"><span className="selection"><span className="select2-selection select2-selection--single" role="combobox" aria-haspopup="true" aria-expanded="false" tabindex="0" aria-labelledby="select2-golf_incentive_id-container"><span class="select2-selection__rendered" id="select2-golf_incentive_id-container" title="--Select--"></span><span className="select2-selection__arrow" role="presentation"><b role="presentation"></b></span></span></span><span class="dropdown-wrapper" aria-hidden="true"></span></span>
                          </div>    
                        </div>    
                    </div>
                    <div className="col-lg-6">
                      <div className="form-group">    
                          <div className="input-group popup">
                          <div className="input-group-addon" className={classes.Labledv}><label for="manufacturer_id">Manufacturer</label></div>
                            <select name="manufacturer_id" id="manufacturer_id" className="js-example-basic-single form-control select2-hidden-accessible" tabindex="-1" aria-hidden="true">
                                <option value="">--Select--</option>
                                                <option value="3">Callaway</option>
                                                <option value="5">TaylorMade</option>
                                                <option value="6">Cobra Inc</option>
                                                <option value="7">Mizuno</option>
                                                <option value="21">Ramesh Kumar Dodda</option>
                                                
                            </select><span className="select2 select2-container select2-container--default" dir="ltr"><span className="selection"><span className="select2-selection select2-selection--single" role="combobox" aria-haspopup="true" aria-expanded="false" tabindex="0" aria-labelledby="select2-manufacturer_id-container"><span className="select2-selection__rendered" id="select2-manufacturer_id-container" title="--Select--"></span><span className="select2-selection__arrow" role="presentation"><b role="presentation"></b></span></span></span><span className="dropdown-wrapper" aria-hidden="true"></span></span>
                          </div>    
                        </div>    
                    </div>    
                  </div>    
                  <div className="col-lg-2 col-xs-6">
                    <button  type="submit" id="myFilter" className="btn btn-success btn-block"><i className="fa fa-filter"></i> Filter</button>
                  </div>
                  <div className="col-lg-2 col-xs-6">
                    <a href="javascript:void(0);" id="clearFilter" className="btn btn-danger btn-block"><i className="fa fa-eraser"></i> Clear</a>
                  </div>
                  </div>
                  </form>
                </div>
            </section>
               <div className={classes.ContentBlock}>
            {this.isEmpty(items)?items.map(item =>(
          <div className="col-md-3">
        				<div className="thumbnail">
        				  <a href={'/IncentiveDetails/'+ item.id} data-id={item.id}>
        					<img src={item.photo_path} alt="M3 Driver" className={classes.ImageHeight} style={maxHeight} />
        					<div className="caption">
        					  <p className="text-center">{item.title}</p>
        					</div>
        				  </a>
        				</div>
			     </div>
              )):(<div className="col-md-12"><div className="col-md-6">No Data Found</div></div>)};
          </div>
          </div>
        );
    }
  }
}

export default Incentives;
