import React, { Component } from 'react';
import classes from './Dashboard.css';
import {
  BrowserRouter as Router,
  Route,
  Link,
  Redirect,
  withRouter
} from "react-router-dom";
var displayNone = {
  display: 'none' 
};
var AdminMenuBack = {
  backgroundColor: '#0075B8'
}
var DashboardHeight={
  minHeight: '400px'
}
var sideBar ={
  borderRadius: '0',
    borderBottom: '1px solid #ffffff70',
    lineHeight: '30px',
}

class Dashboard extends Component {
  constructor(props){
    super(props);
    this.state = {
      items : [],
      user_id: sessionStorage.getItem('userId'),
      isLoaded : false,
    }
  }


   componentDidMount(){
  fetch('http://codelive.info/pbg_golf/api/user_dashboard_counters_post', {
        method: 'POST',
    headers: {
              //"Content-Type": "application/json",
               "Content-Type": "application/x-www-form-urlencoded",
      },
        body:  "user_id="+this.state.user_id,
    })
    .then(res => res.json())
    .then(json => {
      
       this.setState({
          isLoaded : true,
          items : json.data,
        })
    });
  }

   goToProfile(){
    window.location = '/Profile';
  }
   goToOrder(){
    window.location = '/Order';
  }
   goToCart(){
    window.location = '/Cart';
  }


  render(){
  if(!sessionStorage.getItem('isLogin')){ window.location = '/Login' }
  var { isLoaded, items } = this.state;

    if (!isLoaded) {
      return <div> Loading....</div>;
    }
    else{
  return(
      <div className={classes.Dashboard}>
        <section className={classes.AboutMidSection}>
          <Router>
          <div>
          <div className="container-fluid">
              <div className="row">
                  <div className="col-sm-2">
                      <ul className="nav nav-pills nav-stacked " style={AdminMenuBack}>
                          <li exact className={classes.Active}><Link to="/Dashboard" style={sideBar}><i className="fas fa-tachometer-alt fa-fw"></i> Dashboard</Link></li>
                          <li onClick={this.goToProfile}><Link to="/Profile" style={sideBar}><i className="fa fa-user-circle fa-fw"></i> Profile</Link></li>
                          {/*<li><Link to="/Reports" style={sideBar}><i className="fas fa-chart-bar fa-fw"></i> Reports</Link></li>
                          <li><Link to="/Settings" style={sideBar}><i className="fa fa-cog fa-fw"></i> Settings</Link></li>*/}
                          <li onClick={this.goToOrder}><Link to="/Orders" style={sideBar}><i className="fab fa-first-order fa-fw"></i> Orders</Link></li>
                          <li onClick={this.goToCart}><Link to="/Cart" style={sideBar}><i className="fa fa-shopping-cart fa-fw"></i> Cart</Link></li>
                      </ul>
                    </div>
                  <div class="col-sm-10 well" style={{padding: '0px 10px'}}>
                         <div>
                      <h2 class="text-black" style={{marginLeft: '13px'}}>Dashboard</h2>
                      <div class="row" style={DashboardHeight} >
                          <div class="col-lg-3 text-center">
                            <div class="panel panel-warning">
                            <div class="panel-heading"><h4>Your Bag</h4></div>
                            <div class="panel-body"><h3>{items.golfer_bag?items.golfer_bag:0}</h3></div>
                            </div>
                          </div>  
                          <div class="col-lg-3 text-center">
                            <div class="panel panel-info">
                            <div class="panel-heading"><h4>Accepted Orders</h4></div>
                            <div class="panel-body"><h3>{items.golfer_orders?items.golfer_orders:0}</h3></div>
                            </div>
                          </div>  
                          <div class="col-lg-3 text-center">
                            <div class="panel panel-primary">
                            <div class="panel-heading"><h4>Completed Orders</h4></div>
                            <div class="panel-body"><h3>{items.golfer_orders_completed?items.golfer_orders_completed:0}</h3></div>
                            </div>
                          </div>  
                          <div class="col-lg-3 text-center">
                            <div class="panel panel-danger">
                            <div class="panel-heading"><h4>Pending Orders</h4></div>
                            <div class="panel-body"><h3>{items.golfer_orders_not_accepted?items.golfer_orders_not_accepted:0}</h3></div>
                            </div>
                          </div>            
                    </div>
                    </div>
                  </div>
            </div>
          </div>
</div>
</Router>
</section>
      </div>
    );
}
}
};



export default withRouter(Dashboard);
