import React, { Component } from 'react';
import classes from './SelectIncentive.css';
import './SelectIncentive.css';
import image from '../../bg.jpg';
import $ from 'jquery';
import Notifications, {notify} from 'react-notify-toast';

var styles = {
color:'violet',
backgroundImage: 'url('+image+')'
};



class SelectIncentive extends Component {

  constructor(props){
    super(props);
    this.state = {
      items : [],
      course_id: '',
      isLoaded : false,
      items1 :false,
      items2 :false,
      items3 :false,
      items4 :false, 
    }
    
  }

  isEmpty(obj) {
    for(var key in obj) {
        if(obj.hasOwnProperty(key))
            return true;
    }
    
    return false;

  }

  colSpanset(){
    
    $('.club_tr_no_data').attr('colspan',$('.club_tr th').length);
  }
  dismissModal(){
 
  window.$('#pvg_golf_modal').modal('hide');
  $('#pvg_golf_modal').removeClass('in');
  $('#inc_club_id').val('');
  $('#inc_shaft_id').val('');
}
  autoSelectClubShaft(){
    var club = $('#inc_club_id').val();
    var shaft =  $('#inc_shaft_id').val();
    
    if(club != ""){
    $('.exist.inc_clubs input[value='+club+']').trigger('click');
    }else{
      $( "tr:first-child td:first-child input").trigger('click');
      
    }
    if(shaft != ""){
    $('.exist.inc_shafts input[value='+shaft+']').trigger('click');
    }else{
      $( "tr:first-child td:first-child input").trigger('click');
    }
  }

  selectClubCart(value){
  
  var x = $(".inc_clubs input[value="+value+"]").get();
  var y = $(x).parent().parent().html();
  $('#inc_club_id').val(value);
  $('.club_body').html(y);
  $('.club_body .exist').remove();
  $('.club_select').removeClass('hide');
  
}

selectShaftCart(value){
  var x = $(".inc_shafts input[value="+value+"]").get();
  var y = $(x).parent().parent().html();
  
  $('#inc_shaft_id').val(value);
  $('.shaft_body').html(y);
  $('.shaft_body .exist').remove();
  $('.shaft_select').removeClass('hide');
  
}

  handleManufacturerData(ev,category_id){
    
    fetch('http://codelive.info/pbg_golf/api/get_manufacturer_post', {
      method: 'POST',
      headers: {
              //"Content-Type": "application/json",
               "Content-Type": "application/x-www-form-urlencoded",
      },
      body: "category_id="+category_id,
  
    })
    .then(res => res.json())
    .then(json => {
        this.setState({
       items1 : json,
      })
      $("#category_id").val(category_id);  
    $("#manufacturer_data").removeClass("hide");
      
    });
    
  }

  handleIncentiveCategory(ev,manufacturer_id){
   
    var cat_id = $("#category_id").val();
    fetch('http://codelive.info/pbg_golf/api/get_incentive_category_post', {
      method: 'POST',
      headers: {
              //"Content-Type": "application/json",
               "Content-Type": "application/x-www-form-urlencoded",
      },
      body: "category_id="+cat_id+"&manufacturer_id="+manufacturer_id,
  
    })
    .then(res => res.json())
    .then(json => {
      this.setState({
        items2 : json,
      })
       $("#manufacturer_id").val(manufacturer_id);
       $("#category_id").val(cat_id);
       $("#main_incentive_data").removeClass("hide");
    });
  }

  handleIncentiveData(ev,inc_cat_id){

  
    
     var cat_id = $("#category_id").val();
     var manufacturer_id =$("#manufacturer_id").val();
   
    fetch('http://codelive.info/pbg_golf/api/get_incentives_post', {
      method: 'POST',
      headers: {
              //"Content-Type": "application/json",
               "Content-Type": "application/x-www-form-urlencoded",
      },
      body: "incentive_category_id="+inc_cat_id+"&manufacturer_id="+manufacturer_id,
  
    })
    .then(res => res.json())
    .then(json => {
      this.setState({
       items3 : json,
      })
      $("#manufacturer_id").val(manufacturer_id);
       $("#category_id").val(cat_id);
      $("#main_incentive_id").val(inc_cat_id);
      $("#sub_incentive_data").removeClass('hide');
     
    });
  }

  
  handleIncentiveModal(ev,inc_id){
    var cat_id = $("#category_id").val();
     var manufacturer_id =$("#manufacturer_id").val();
     var inc_cat_id =$("#main_incentive_id").val(); 
     fetch('http://codelive.info/pbg_golf/api/get_incentive_customize_form_post', {
      method: 'POST',
      headers: {
              //"Content-Type": "application/json",
               "Content-Type": "application/x-www-form-urlencoded",
      },
      body: "incentive_id="+inc_id,
    })
    .then(res => res.json())
    .then(json => {
      this.setState({
        isLoaded : true,
        items4 : json.data,
      })
      $("#manufacturer_id").val(manufacturer_id);
      $("#category_id").val(cat_id);
      $("#main_incentive_id").val(inc_cat_id); 
      $("#sub_incentive_id").val(inc_id);
      this.colSpanset();
      this.autoSelectClubShaft();
      window.$('#pvg_golf_modal').modal('show');
      $('#pvg_golf_modal').addClass('in');

    });
  }
  addToCart(){
      const data = new FormData($("#addForm")[0]);
     fetch('http://codelive.info/pbg_golf/api/create_package_post',{
      method: 'POST',
      body: data
     })
    .then(res => res.json())
    .then(json => {
      if(json.replyStatus === "success"){

          sessionStorage.setItem('cart_id',json.data.cart_id);
          sessionStorage.setItem('inc_cart_id',json.data.inc_cart_id);
          notify.show(json.replyMsg,"success", 5000, "green");
          window.location = '/Review';
      }
    });
  }
  

  componentDidMount(){
    
    fetch('http://codelive.info/pbg_golf/api/get_category_post')
    .then(res => res.json())
    .then(json => {
      this.setState({
        isLoaded : true,
        items : json,
      })
    });

  }
  render() {
    
    var { isLoaded, items,items1,items2,items3,items4 } = this.state;

    if (!isLoaded) {
      return <div> Loading....</div>;
    }
    else{
          return (
            
          <div className={classes.SelectIncentive}>
          <Notifications options={{zIndex: 999, top: '100px'}} />
        <section class="fix">
        <div className={classes.Wizard}>
          <div className={classes.WizardInner}>
                <div className={classes.ConnectingLine}></div>
                <ul className="nav nav-tabs" role="tablist">

                    <li role="presentation" className={classes.ActiveTabs}>
                        <a className={classes.ClickTab} href="javascirpt:void(0);" data-toggle="tab" aria-controls="step1" role="tab" title="" data-original-title="Step 1">
                            <span className={classes.RoundTab}></span>
                        </a>
            <p>Select Course</p>
            
                    </li>
                    <li role="presentation" className={classes.ActiveTabs}>
                        <a className={classes.ClickTab} href="javascirpt:void(0);" data-toggle="tab" aria-controls="step2" role="tab" title="" data-original-title="Step 2">
                            <span className={classes.RoundTab}></span>            
                        </a>
            <p>Select Incentive</p>
                    </li>
                    <li role="presentation">
                        <a className={classes.ClickTab} href="javascirpt:void(0);" data-toggle="tab" aria-controls="step3" role="tab" title="" data-original-title="Step 3">
                            <span className={classes.RoundTab2}></span>
                        </a>
            <p>Review</p>
                    </li>

                    <li role="presentation">
                        <a className={classes.ClickTab} href="javascirpt:void(0);" data-toggle="tab" aria-controls="complete" role="tab" title="" data-original-title="Complete">
                            <span className={classes.RoundTab2}></span>
                        </a>
            <p>Payment</p>
                    </li>
                </ul>
            </div>
            </div>
            </section>
            <form role="form" id="addForm" >
      <div class="tab-content">
        
          
          
          <input type="hidden" value="" name="category_id" id="category_id" />
          <input type="hidden" value="" name="manufacturer_id" id="manufacturer_id" />
          <input type="hidden" value="" name="incentive_category_id" id="main_incentive_id" />
          <input type="hidden" value="" name="incentive_id" id="sub_incentive_id" />

          <input type="hidden" value={sessionStorage.getItem('userId')} name="user_id" id="user_id" />        
          <input type="hidden" value="" name="inc_club_id" id="inc_club_id" />
          <input type="hidden" value="" name="inc_shaft_id" id="inc_shaft_id" />
          <input type="hidden" value="" name="inc_qunt" id="inc_qunt" />
          <input type="hidden" value={sessionStorage.getItem('course_ids')} name="course_ids" id="course_ids" />
          <input type="hidden" value={sessionStorage.getItem('playing_time')} name="play_check" id="play_check" />
          <input type="hidden" value={sessionStorage.getItem('round_id')} name="round_id" id="round_id" />
          <input type="hidden" value="1" name="round_qty" id="round_qty" />
          <input type="hidden" value={sessionStorage.getItem('rc_id')} name="rc_id" id="rc_id" />
          <input type="hidden" value="" name="cart_id" id="cart_id" />        
        
    <div class="tab-pane active in" role="tabpanel" id="step2">
    <div className="container">
          <ul className={classes.Block}>
            <li><a href="http://codelive.info/pbg_golf/back-course/1" type="button" class="btn btn-default prev-step back">Back</a></li>
            
            
            
          </ul>
          </div>
          
          <div className={classes.TylorMad}>
            <div class="container">
            <div class="row">
              <div class="col-md-6 col-sm-6 col-xs-12">
              <div class="row">
                <div class="col-md-4 col-sm-4 col-xs-12" id="category_data">
                {items.data.map(item =>(
                    <a href="javascript:void(0)" data-id={item.id} onClick={((e) => this.handleManufacturerData(this,item.id))} className={classes.ButtonMad} >{item.category_name}</a>
              ))} 
              </div>
                <div class="col-md-4 col-sm-4 col-xs-12 hide"   id="manufacturer_data">

                   {items1?items1.data.map(item =>(
                     <a href="javascript:void(0)" data-item-name={item.full_name} data-id={item.id} onClick={((e) => this.handleIncentiveCategory(e,item.id))} className={classes.ButtonMad} id="btn">{item.full_name}</a>
              )):''}


                </div>
                
                
                <div class="col-md-4 col-sm-4 col-xs-12 hide"  id="main_incentive_data">
               
                {items2?items2.data.map(item =>(
                    <a href="javascript:void(0)" data-item-name="Drivers" data-image="http://codelive.info/pbg_golf/uploads/incentive_images/incentive_img_1536560623.png" data-id={item.id} onClick={((e) => this.handleIncentiveData(e,item.id))} className={classes.ButtonMad}>{item.incentive_name}</a>
              

              )):''} 

              </div>
                
               </div>
              </div>

             <div class="col-md-6 col-sm-6 col-xs-12 hide"  id="sub_incentive_data"><div class="row">
             
             {items3?items3.data.map(item =>(
                
                   <div class="col-sm-6 col-md-6 col-xs-12 text-center marB30">
                    <a className={classes.ItemImgbox} href="javascript:void(0)" onClick={((e) => this.handleIncentiveModal(e,item.id))}>
                      <center className={classes.CartSubcatImg} style={styles}>
                      <img src={item.photo} alt="image"/>
                      </center>
                      <h4 className={classes.ButtonMad}>{item.title}</h4>
                    </a>
                  </div>
              

              )):''} 
      
      

<div class="clearfix"></div>
</div>


 </div>
            </div>
            </div>
          </div>
        </div>
        
        <div class="clearfix"></div>
      </div>
    {items4?

      <div class="modal fade" id="pvg_golf_modal" tabindex="-1" role="dialog" aria-labelledby="myModalLabel" aria-hidden="true">
        <div class="modal-dialog">
          <div class="modal-content modal-content-one">
            <div class="close_btn modal-header">
              <button type="button" className={classes.close} onClick={this.dismissModal}><span aria-hidden="true"></span><span class="sr-only">Close</span></button>                 
            </div>
            <div class="modal-body">
<div class="col-sm-5 border_right_dv">
  <div class="golf_firstpart">
    <div className={classes.GolfHead}>
      <h1>{items4.incentive.title}</h1>
    </div>
    <div className={classes.GolfImg}>
      <img src={items4.incentive.photo_path} />
    </div>            
  </div>
  <div className={classes.GolfSecond}>
    <div className={classes.SecondHead}>
      <h4>Selection</h4>
    </div>
    <div className={classes.SecondCon}>
      <div className={classes.SecondHead}>
        <h5>Club</h5>
      </div>
      <div class="table-responsive">          
        <table class="table">
        <thead>
          <tr>
            
          {items4.club_item_name_array.indexOf("club_name")!= -1?(items4.incentive.golf_incentive_id==1?(<th>Model</th>):(<th>Name</th>)):''}
          {items4.club_item_name_array.indexOf("loft")!= -1?(<th>Loft</th>):'' }
          {items4.club_item_name_array.indexOf("hand")!= -1?(<th>Availability</th>):'' }
          {items4.club_item_name_array.indexOf("bounce")!= -1?(<th>Bounce</th>):'' }
           {items4.club_item_name_array.indexOf("grind")!= -1?(<th>Grind</th>):'' }
          {items4.club_item_name_array.indexOf("groove_type")!= -1?(<th>Groove Type</th>):'' }
          {items4.club_item_name_array.indexOf("grip")!= -1?(<th>Grip</th>):'' }
          {items4.club_item_name_array.indexOf("length")!= -1?(items4.incentive.golf_incentive_id==6?(<th>Standard Length</th>):(<th>Length</th>)):'' }
          {items4.club_item_name_array.indexOf("lie")!= -1?(<th>Lie</th>):'' } 
          {items4.club_item_name_array.indexOf("angle")!= -1?(<th>Angle</th>):'' } 
          {items4.club_item_name_array.indexOf("volume")!= -1?(<th>CC</th>):'' } 
          {items4.club_item_name_array.indexOf("offset")!= -1?(<th>Offset</th>):'' } 
          {items4.club_item_name_array.indexOf("graphite_swing_weight")!= -1?(<th>Graphite Swing Weight</th>):'' } 
          {items4.club_item_name_array.indexOf("steel_swing_weight")!= -1?(<th>Steel Swing Weight</th>):'' } 
          {items4.club_item_name_array.indexOf("swing_weight")!= -1?(<th>Swing Weight</th>):'' }   
           </tr>
        </thead>                
        <tbody class="club_body"> 
               
        </tbody>
        </table>
      </div>
    </div>
    <div className={classes.SecondCon}>
      <div className={classes.SecondHead}>
        <h5>Shaft</h5>
      </div>            
      <div class="table-responsive">          
        <table class="table">
        <thead>
          <tr>
         
            {items4.shaft_item_name_array.indexOf("shaft_name")!= -1?(<th>Name</th>):'' }
            {items4.shaft_item_name_array.indexOf("gender")!= -1?(<th>Gender</th>):'' }
            {items4.shaft_item_name_array.indexOf("manufacturer")!= -1?(<th>Manufacturer</th>):'' }
            {items4.shaft_item_name_array.indexOf("style")!= -1?(<th>Style</th>):'' }
            {items4.shaft_item_name_array.indexOf("weight")!= -1?(<th>Weight</th>):'' }        
            {items4.shaft_item_name_array.indexOf("flex_id")!= -1?(<th>Flex</th>):'' }
          </tr>
        </thead>                
        <tbody class="shaft_body">
          
        </tbody>
        </table>
      </div>              
    </div>
    <div class="second_con hide">
      <label>Quantity</label>
      <input type="number" onclick="change_qunt(this);" class="text-center quantity_dv" value="1" name="inc_qunt" id="inc_qunt" />              
    </div>
    
    <div className={classes.GolfBtn}>
      <button type="button" className={classes.BtnInfo} onClick={this.addToCart}>Create Packages</button>
      <button type="button" className={classes.BtnDanger} onClick={this.dismissModal} >Close</button>
    </div>
  </div>
</div>
<div class="col-sm-7">
  <div class="third_con">
    <div className={classes.ThirdHead}>
      <h4>Club</h4>
    </div>
    <div className={classes.ThirdTable}>
      <div class="table-responsive">          
        <table class="table">
        <thead>
          <tr class="club_tr">
              <th>Select</th>
               
              {items4.club_item_name_array.indexOf("club_name")!= -1?(items4.incentive.golf_incentive_id==1?(<th>Model</th>):(<th>Name</th>)):''}
              {items4.club_item_name_array.indexOf("loft")!= -1?(<th>Loft</th>):'' }
              {items4.club_item_name_array.indexOf("hand")!= -1?(<th>Availability</th>):'' }
              {items4.club_item_name_array.indexOf("bounce")!= -1?(<th>Bounce</th>):'' }
               {items4.club_item_name_array.indexOf("grind")!= -1?(<th>Grind</th>):'' }
              {items4.club_item_name_array.indexOf("groove_type")!= -1?(<th>Groove Type</th>):'' }
              {items4.club_item_name_array.indexOf("grip")!= -1?(<th>Grip</th>):'' }
              {items4.club_item_name_array.indexOf("length")!= -1?(items4.incentive.golf_incentive_id==6?(<th>Standard Length</th>):(<th>Length</th>)):'' }
              {items4.club_item_name_array.indexOf("lie")!= -1?(<th>Lie</th>):'' } 
              {items4.club_item_name_array.indexOf("angle")!= -1?(<th>Angle</th>):'' } 
              {items4.club_item_name_array.indexOf("volume")!= -1?(<th>CC</th>):'' } 
              {items4.club_item_name_array.indexOf("offset")!= -1?(<th>Offset</th>):'' } 
              {items4.club_item_name_array.indexOf("graphite_swing_weight")!= -1?(<th>Graphite Swing Weight</th>):'' } 
              {items4.club_item_name_array.indexOf("steel_swing_weight")!= -1?(<th>Steel Swing Weight</th>):'' } 
              {items4.club_item_name_array.indexOf("swing_weight")!= -1?(<th>Swing Weight</th>):'' }
          </tr>
        </thead>                
        <tbody>
        {this.isEmpty(items4.incentive_club)?items4.incentive_club.map(inc_club =>(
          <tr>  
            <td class="exist inc_clubs"><input type="radio" name="select2" value={inc_club.id} onClick={((e) => this.selectClubCart(inc_club.id))} /></td>
           
            {items4.club_item_name_array.indexOf("club_name")!= -1?(<td>{inc_club.club_name}</td>):''}
            {items4.club_item_name_array.indexOf("loft")!= -1?(<td>{inc_club.loft}</td>):'' }
            {items4.club_item_name_array.indexOf("hand")!= -1?(inc_club.hand==1?(<td>Left</td>):(<td>Right</td>)):'' }
            {items4.club_item_name_array.indexOf("bounce")!= -1?(<td>{inc_club.bounce}</td>):'' }
             {items4.club_item_name_array.indexOf("grind")!= -1?(<td>{inc_club.grind}</td>):'' }
            {items4.club_item_name_array.indexOf("groove_type")!= -1?(<td>{inc_club.groove_type}</td>):'' }
            {items4.club_item_name_array.indexOf("grip")!= -1?(<td>{inc_club.grip}</td>):'' }
            {items4.club_item_name_array.indexOf("length")!= -1?(<td>{inc_club.length}</td>):'' }
            {items4.club_item_name_array.indexOf("lie")!= -1?(<td>{inc_club.lie}</td>):'' } 
            {items4.club_item_name_array.indexOf("angle")!= -1?(<td>{inc_club.angle}</td>):'' } 
            {items4.club_item_name_array.indexOf("volume")!= -1?(<td>{inc_club.volume}</td>):'' } 
            {items4.club_item_name_array.indexOf("offset")!= -1?(<td>{inc_club.offset}</td>):'' } 
            {items4.club_item_name_array.indexOf("graphite_swing_weight")!= -1?(<td>{inc_club.graphite_swing_weight}</td>):'' } 
            {items4.club_item_name_array.indexOf("steel_swing_weight")!= -1?(<td>{inc_club.steel_swing_weight}</td>):'' } 
            {items4.club_item_name_array.indexOf("swing_weight")!= -1?(<td>{inc_club.swing_weight}</td>):'' }
          </tr> 
          )):(<tr>                  
            <td class="club_tr_no_data" colspan="7">No data Available.</td>
          </tr>)}  
                     
        </tbody>
        </table>
      </div>
    </div>
    <div className={classes.ThirdHead}>
      <h4>Shaft</h4>
    </div>
    <div className={classes.ThirdTable}>
      <div class="table-responsive">          
       <table class="table">
        <thead>
          <tr>
            <th>Select</th>
            {items4.shaft_item_name_array.indexOf("shaft_name")!= -1?(<th>Name</th>):'' }
            {items4.shaft_item_name_array.indexOf("gender")!= -1?(<th>Gender</th>):'' }
            {items4.shaft_item_name_array.indexOf("manufacturer")!= -1?(<th>Manufacturer</th>):'' }
            {items4.shaft_item_name_array.indexOf("style")!= -1?(<th>Style</th>):'' }
            {items4.shaft_item_name_array.indexOf("weight")!= -1?(<th>Weight</th>):'' }        
            {items4.shaft_item_name_array.indexOf("flex_id")!= -1?(<th>Flex</th>):'' }
          
          </tr>
        </thead>                
        <tbody>
        {this.isEmpty(items4.incentive_shaft)?items4.incentive_shaft.map(inc_shaft =>(
            <tr>
          <td class="exist inc_shafts"><input type="radio"  name="select1" value={inc_shaft.id} onClick={((e) => this.selectShaftCart(inc_shaft.id))}/></td>
          {items4.shaft_item_name_array.indexOf("shaft_name")!= -1?(<td>{inc_shaft.shaft_name}</td>):'' }
          {items4.shaft_item_name_array.indexOf("gender")!= -1?(inc_shaft.gender==1?(<td>Mens</td>):(<td>Womens</td>)):'' }
          {items4.shaft_item_name_array.indexOf("manufacturer")!= -1?(<td>{inc_shaft.manufacturer}</td>):'' }
          {items4.shaft_item_name_array.indexOf("style")!= -1?(<td>{inc_shaft.style}</td>):'' }
          {items4.shaft_item_name_array.indexOf("weight")!= -1?(<td>{inc_shaft.weight}</td>):'' }        
          {items4.shaft_item_name_array.indexOf("flex_id")!= -1?(<td>{inc_shaft.shaft_flex_name}</td>):'' }
          </tr>
        )):(<tr>                  
            <td colspan="7">No data Available.</td>
          </tr>)} 
        </tbody>
        </table>
        </div>
    </div>            
  </div>
</div>
</div>
            </div>
        </div>
      </div>:''}
      </form>
          </div>
        );
    }
  }
}

export default SelectIncentive;
