import React, { Component } from 'react';
import classes from './Supports.css';
import Home from '../Home/Home';
import OTP from '../OTP/OTP';
import Notifications, {notify} from 'react-notify-toast';
import $ from 'jquery';
import {
  BrowserRouter as Router,
  Route,
  Link,
  Redirect,
  withRouter
} from "react-router-dom";


class Supports extends Component {

  constructor(props){
    super(props);
    this.state = {
      fname: '',
      lname: '',
      email: '',
      subject: '',
      comments: '',
      isLoaded : false,
    }
  }

  

  handleSubmit(event){
  	event.preventDefault();
  	const data = new FormData(event.target);
    console.log(data);
   fetch('http://codelive.info/pbg_golf/api/support_submit_post', {
      method: 'POST',
      body: data
    })
   .then(res => res.json())
    .then(json => {
    	
      if(json.replyStatus === 'success'){
      	notify.show(json.replyMsg,"success", 5000, "green");
        $("#addForm")[0].reset();
  		}else{

          notify.show(json.replyMsg,"error", 5000, "red");
          
      }
   	 })
    .catch((error)=>{
    	console.error(error);
    })
  }

  componentDidMount(){
    fetch('http://codelive.info/pbg_golf/api/support_post',{
      method: 'POST',
      headers: {
              //"Content-Type": "application/json",
               "Content-Type": "application/x-www-form-urlencoded",
      },
      
    })
    .then(res => res.json())
    .then(json => {
      this.setState({
        isLoaded : true,
        items : json.data,
      })
    });
  }
  render() {
    
    var { isLoaded, items, fname } = this.state;

    if (isLoaded) {
      return (
      	<div className={classes.Supports}>
        <Notifications options={{zIndex: 200, top: '100px'}} />
      	<div class="col-sm-12">
			<p>Please fill out the contact form below to send our customer support a message. Or you can email us directly at&nbsp;support@parbirdiegolf.com.
			</p>
		</div>
		<div class="mTB20 col-sm-6">
				<form id="addForm" class="form-horizontal" method="post" action="" enctype="multipart/form-data" onSubmit={this.handleSubmit}>
					<div class="form-group">
						<label><span class="text-danger font_12">* </span> First Name</label>
						<input type="text" name="fname"  class="form-control" />
					</div>
					<div class="form-group">
						<label><span class="text-danger font_12">* </span> Last Name</label>
						<input type="text" name="lname"  class="form-control"  />
					</div>		
					<div class="form-group">
						<label><span class="text-danger font_12">* </span> Email</label>
						<input type="email" name="email"  class="form-control"  />
					</div>		
					<div class="form-group">
						<label><span class="text-danger font_12">* </span> Subject</label>
						<input type="text" name="subject" class="form-control" />
					</div>	
					<div class="form-group">
						<label><span class="text-danger font_12">* </span> Comments</label>
						<textarea name="comments" class="form-control"  ></textarea>
					</div>					
					<hr/>
					<div class="form-group">			  
						<button type="submit" id="sub_btn" class="btn btn-info btn-flat">Submit</button>					
					</div>							
				</form>
			</div>
    </div>
      	)}
      else{
        return <div> Loading....</div>;
      }
}
}

export default withRouter(Supports);
