import React from "react";
import classes from './Slider.css';
import "react-bootstrap-carousel/dist/react-bootstrap-carousel.css";
import RBCarousel from "react-bootstrap-carousel";

const styles = { height: 400, width: "100%" };

class Slider extends React.PureComponent {
  constructor(props) {
    super(props);
    this.state = {
      autoplay: true
    };
  }
 
  _changeIcon = () => {
    let { leftIcon, rightIcon } = this.state;
    leftIcon = leftIcon ? undefined : <span className="glyphicon glyphicon-glass" />;
    rightIcon = rightIcon ? undefined : <span className="glyphicon glyphicon-music" />;
    this.setState({ leftIcon, rightIcon });
  };
  render() {
    let { leftIcon, rightIcon } = this.state;
    return (
      <div className="container-fluit">
        <Row>
          <Col span={12} style={{ marginTop: 20 }}>
            <RBCarousel
              animation={true}
              autoplay={this.state.autoplay}
              slideshowSpeed={2500}
              defaultActiveIndex={0}
              leftIcon={leftIcon}
              rightIcon={rightIcon}
              onSelect={this.onSelect}
              ref={r => (this.slider = r)}
            >
              <div style={{ height: 500 }}>
                <img
                  style={{ width: "100%", height: "100%" }}
                    src="images/slider1.jpg"
                />
                <div className={classes.BsSliderOverlay}></div>
              </div>
              <div style={{ height: 500 }}>
                <img
                  style={{ width: "100%", height: "100%" }}
                    src="images/slider1.jpg"
                />
                <div className={classes.BsSliderOverlay}></div>
              </div>
              <div style={{ height: 500 }}>
                <img
                  style={{ width: "100%", height: "100%" }}
                    src="images/slider1.jpg"
                />
                <div className={classes.BsSliderOverlay}></div>
              </div>
            </RBCarousel>
            <div className="carousel-caption CarouselCaption" >
                <div className="container">
                    <div className="row">
                        <div className={classes.SlideText}>
                            <h1 data-animation="animated zoomInRight">Play Golf and Receive <br/>FREE Equipment, Electronics or Travel.</h1>
                            <h3 data-animation="animated zoomInRight">"ParBirdieGolf.com is changing  the playing and purchasing expectations of golfers everywhere"</h3>
                            <div className={classes.BtnLearnmoreDv}>
                              <a href="#" className={classes.SearchBtn}>Learn How</a>
                            </div>
                        </div>
                    </div>
                </div>
            </div>
            <div className={classes.BrandLogos}>
                          <h3>Our Strategic Partners include:</h3>
                            <ul>
                              <li><a href="javascript:void(0)"><img className="img-responsive" src="images/brand1.jpg" alt="" /></a></li>
                              <li><a href="javascript:void(0)"><img className="img-responsive" src="images/brand2.jpg" alt="" /></a></li>
                              <li><a href="javascript:void(0)"><img className="img-responsive" src="images/brand3.jpg" alt="" /></a></li>
                              <li><a href="javascript:void(0)"><img className="img-responsive" src="images/brand4.jpg" alt="" /></a></li>
                            </ul>
                        </div>
          </Col>
        </Row>
      </div>
    );
  }
}

/**
 *  Boostrap Component
 */
const Row = props => <div className="row">{props.children}</div>;
const Col = props => (
  <div className={`col-xs-${props.span}`} style={props.style}>
    {props.children}
  </div>
);
const Button = props => {
  const { style, bsStyle, onClick } = props;
  const className = bsStyle ? `btn btn-${bsStyle}` : "btn";
  return (
    <button style={style} className={className} onClick={onClick}>
      {props.children}
    </button>
  );
};

export default Slider;