import React, { Component } from 'react';
import classes from './Login.css';
import Home from '../Home/Home';
import OTP from '../OTP/OTP';
import image from '../../bg.jpg';
import $ from 'jquery';
import Notifications, {notify} from 'react-notify-toast';
import {
  BrowserRouter as Router,
  Route,
  Link,
  Redirect,
  withRouter
} from "react-router-dom";

var styles = {
color:'violet',
backgroundImage: 'url('+image+')'
};
var styles2 = {
	backgroundColor: '#DD4B39',
	color: 'white',
	borderRadius : '5px',
	padding : '5px',
}
var fontSize = {
	fontSize: '18px',
}

class Login extends Component {

  constructor(props){
    super(props);

    this.state = {
      email_phone: '',
      password: '',
      isLoaded : false,
    }
  }

  
  handleSubmit = (event) =>{
  	const { history } = this.props;
  	history.push('hello')


  	event.preventDefault();
  	const data = new FormData(event.target);
   fetch('http://codelive.info/pbg_golf/api/login_post', {
      method: 'POST',
      body: data
    })
   .then(res => res.json())
    .then(json => {
    	$(".list-unstyled").html("");
      if(json.replyStatus === 'success'){
      	sessionStorage.setItem('userId', json.data.id);
      	sessionStorage.setItem('userName', json.data.fname+" "+json.data.lname);
      	sessionStorage.setItem('isLogin',true);
      	
      	notify.show(json.replyMsg,"success", 5000, "green");
      	window.location = '/Profile';
      	
		}else if(json.replyStatus === 'otp_auth'){
			sessionStorage.setItem('email_phone', $("input[name=email_phone]").val());
			sessionStorage.setItem('password', $("input[name=password]").val());
      		window.location = '/OTP';
      	}
      else if(json.replyStatus === 'error_2') {
      	$(".list-unstyled").html("<li>"+json.replyMsg+"</li>");
					$("#new_error_div").removeClass('hide');
					$("#sub_button").html('Login'); 
      }else{
      		var error = JSON.parse(json.replyMsg);
      		$.each(error, function(k, v) { 
					var er = v; 
					$(".list-unstyled").append("<li>"+er+"</li>");
					$("#new_error_div").removeClass('hide');
					$("#sub_button").html('Login'); 
				});
      	}
   	 })
    .catch((error)=>{
    	console.error(error);
    })
  }
  render() {
    
    var { isLoaded, items } = this.state;

    if (!isLoaded) {
      return (<div className={classes.Login} style={styles}>
      	<Notifications options={{zIndex: 200, top: '100px'}} />
				<div className={classes.LoginBox}>
				  <div className={classes.LoginBoxBody}>
				  <div className={classes.LoginLogo}>
				      <a href="http://codelive.info/pbg_golf/"><img src="http://codelive.info/pbg_golf/admin_assets/img/logo/1539007624.png" alt="" /></a>
				  </div>
				    <img src="http://codelive.info/pbg_golf/uploads/SMS-512.png" className="img-responsive hide otp_box"  />
					
					
					<p className={classes.LoginBoxMsg}>Sign in to start your session</p>
					<p className={classes.LoginBoxMsg, classes.otp_box, classes.hide}>An OTP has been sent to your Email. Please enter the OTP below for Successful Login</p>
					<div className={classes.ErrorHolder}>
						<div id="new_error_div" className="list3 hide" style={styles2}>
							<h6 style={fontSize}>Instructions</h6>
							<ul className="list-unstyled">
								
							</ul>
						</div>
					</div>
					<div className="login_b ">
				    <form id={classes.LoginForm} method="post" onSubmit={this.handleSubmit}>
				      <div className="form-group has-feedback">
				        <input type="text" name="email_phone" id={classes.EmailPhone} className="form-control" placeholder="Email/Phone"  />
				      </div>
				      <div className="form-group has-feedback">
				        <input type="password" name="password" id={classes.Password} className="form-control" placeholder="Password"   />
				      </div>
				      <div>
				        <div className="col-xs-12">
				          <div className="checkbox icheck">
				            <label>
				              <input type="checkbox" />
				              Remember Me </label>
				            <Link to='/ForgetPassword' className="pull-right"><i className="fa fa-lock"></i> Forgot Password?</Link> </div>
				        </div>
				        
				        <div className="col-xs-12">
				          <button type="submit" id="sub_btn" className="btn btn-primary btn-block btn-flat">Login</button>
				        </div>
				        
				      </div>
				    </form>
				    <div className="social-auth-links text-center ">
				      <p>- OR -</p>
				      <a href="https://www.facebook.com/v2.2/dialog/oauth?client_id=733595310323172&amp;state=180c52ff57bd917e6368a6220f384e56&amp;response_type=code&amp;sdk=php-sdk-5.6.3&amp;redirect_uri=https%3A%2F%2Fcodelive.info%2Fpbg_golf%2Ffacebook%2Ffb_callback&amp;scope=email" id={classes.facebook} className="btn btn-block btn-social btn-facebook btn-flat"><i className="fab fa-facebook"></i> Sign in using
				      Facebook</a> <a href="https://accounts.google.com/o/oauth2/auth?response_type=code&amp;redirect_uri=http%3A%2F%2Fcodelive.info%2Fpbg_golf%2Fgoogle&amp;client_id=371434461276-84b6r0afrlvas5prndbh35f6f27m763i.apps.googleusercontent.com&amp;scope=https%3A%2F%2Fwww.googleapis.com%2Fauth%2Fplus.login+https%3A%2F%2Fwww.googleapis.com%2Fauth%2Fplus.me+https%3A%2F%2Fwww.googleapis.com%2Fauth%2Fuserinfo.email&amp;access_type=online&amp;approval_prompt=auto" id={classes.google} className="btn btn-block btn-social btn-google btn-flat"><i className="fab fa-google-plus"></i> Sign in using
				      Google+</a> </div>
				   
				    
				    <div className={classes.Mt2}>Don't have an account? <Link to="/Register" className="text-center">Sign Up</Link></div>
					</div>
					<div className="otp_box hide">
					<form id="otpForm">
				      <div className="form-group has-feedback">
						
				        <input type="text" name="login_otp" className="form-control sty1" placeholder="Enter OTP" />
				      </div>
						<input type="hidden" id="otp_email" name="otp_email" value="" />
						<input type="hidden" id="otp_password" name="otp_password" value="" />
				      <div>
				        
				        
				        <div className="col-xs-4 m-t-1">
				          <button type="submit" id="sub_button" className="btn btn-primary btn-block btn-flat">Submit</button>
				        </div>
				      </div>
					  
					  <div className="m-t-2 col-xs-8">
				          <div className="checkbox icheck">
				            <label>
				              <input type="checkbox" name="otp_disable_duration" value="30" />
				              Trust this device for 30 days </label>
				             </div>
				        </div>
				    </form>
				   
				    
				    
					
					</div>
					
					
				  </div>
				</div>
			</div>);
    }
    else{
          return (
           <div>hello</div>
        );
    }
  }
}

export default withRouter(Login);
