import React from 'react';
import classes from './ProcessSection.css';
import image from '../../process_bg.png';

var styles = {
color:'violet',
backgroundImage: 'url('+image+')'
};
const ProcessSection = (props) =>{

			return(
				<div className = {classes.ProcessSection}>
					<section className="process_section padTB100">
						<div className="container">
						<div className="row">
							<div className="col-sm-12 text-center">
								<h2>Process to get started</h2>
								<p></p>
							</div>
						</div>
						
						<div className="text-center" id={classes.PrecessBgpart} style={styles}></div>
						</div>
						<div className="clearfix"></div>

					</section>
				</div>
				);

};
export default ProcessSection;
