import React, { Component } from 'react';
import classes from './Profile.css';
import ProfilePage from '../ProfilePage/ProfilePage';
import Dashboard from '../Dashboard/Dashboard';
import Cart from '../Cart/Cart';

import {
  BrowserRouter as Router,
  Route,
  Link,
  Redirect,
  withRouter
} from "react-router-dom";
var displayNone = {
	display: 'none' 
};
var AdminMenuBack = {
	backgroundColor: '#0075B8'
}
var DashboardHeight={
	minHeight: '400px'
}
var sideBar ={
	borderRadius: '0',
    borderBottom: '1px solid #ffffff70',
    lineHeight: '30px',
}

class Profile extends Component {
  constructor(props){
    super(props);
  }
  goToCart(){
  	window.location = '/Cart';
  }
   goToOrder(){
  	window.location = '/Order';
  }
   goToDashboard(){
  	window.location = '/Dashboard';
  }
  render(){
	if(!sessionStorage.getItem('isLogin')){ window.location = '/Login' }
	return(
			<div className={classes.Profile}>
				<section className={classes.AboutMidSection}>
					<Router>
					<div>
					<div className="container-fluid">
    					<div className="row">
        					<div className="col-sm-2">
		            			<ul className="nav nav-pills nav-stacked " style={AdminMenuBack}>
                					<li onClick={this.goDashboard}><Link to="/Dashboard" style={sideBar}><i className="fas fa-tachometer-alt fa-fw"></i> Dashboard</Link></li>
					                <li className={classes.Active}><Link to="/Profile" style={sideBar}><i className="fa fa-user-circle fa-fw"></i> Profile</Link></li>
					                {/*<li><Link to="/Reports" style={sideBar}><i className="fas fa-chart-bar fa-fw"></i> Reports</Link></li>
					                <li><Link to="/Settings" style={sideBar}><i className="fa fa-cog fa-fw"></i> Settings</Link></li>*/}
					                <li onClick={this.goToOrder}><Link to="/Orders" style={sideBar}><i className="fab fa-first-order fa-fw"></i> Orders</Link></li>
					                <li onClick={this.goToCart}><Link to="/Cart" style={sideBar}><i className="fa fa-shopping-cart fa-fw"></i> Cart</Link></li>
            					</ul>
            				</div>
					      	<div className="col-sm-10 well admin-content">
					      		 
								   <Route exact path="/Profile" component={ProfilePage} />
								 
						  	</div>
						</div>
					</div>
</div>
</Router>
</section>
			</div>
		);
}
};


export default withRouter(Profile);
