import React, { Component } from 'react';
import classes from './ContactUs.css';
//import Home from '../Home/Home';
//import OTP from '../OTP/OTP';
import {
 // BrowserRouter as Router,
 // Route,
  //Link,
  //Redirect,
  withRouter
} from "react-router-dom";


class ContactUs extends Component {

  constructor(props){
    super(props);
    this.state = {
      fname: '',
      lname: '',
      email: '',
      subject: '',
      comments: '',
      isLoaded : false,
    }
  }

  
  handleSubmit(event){
  	event.preventDefault();
  	const data = new FormData(event.target);
    console.log(data);
   fetch('http://codelive.info/pbg_golf/api/support_submit_post', {
      method: 'POST',
      body: data
    })
   .then(res => res.json())
    .then(json => {
    	console.log(json); return false;
      if(json.replyStatus === 'success'){
      	window.location = '/Profile';
		}else if(json.replyStatus === 'otp_auth'){
      		window.location = '/OTP';
      	}
      else{
      		alert(json.replyStatus);
      	}
   	 })
    .catch((error)=>{
    	console.error(error);
    })
  }
  render() {
    
    var { isLoaded } = this.state;

    if (!isLoaded) {
      return (
      	<div className={classes.ContactUs}>
      	<div className="col-sm-12">
			<p>Please fill out the contact form below to send our customer support a message. Or you can email us directly at&nbsp;support@parbirdiegolf.com.
			</p>
		</div>
		<div className="mTB20 col-sm-6">
				<form id="addForm" className="form-horizontal" method="post" action="" enctype="multipart/form-data">
					<div className="form-group">
						<label><span className="text-danger font_12">* </span> First Name</label>
						<input type="text" name="fname" value="" className="form-control" required=""  />
					</div>
					<div className="form-group">
						<label><span className="text-danger font_12">* </span> Last Name</label>
						<input type="text" name="lname" value="" className="form-control" required=""  />
					</div>		
					<div className="form-group">
						<label><span className="text-danger font_12">* </span> Email</label>
						<input type="email" name="email" value="" className="form-control" required="" />
					</div>		
					<div className="form-group">
						<label><span className="text-danger font_12">* </span> Subject</label>
						<input type="text" name="subject" value="" className="form-control" required="" />
					</div>	
					<div className="form-group">
						<label><span className="text-danger font_12">* </span> Comments</label>
						<textarea name="comments" className="form-control" required="" ></textarea>
					</div>					
					<hr/>
					<div className="form-group">			  
						<button type="submit" id="sub_btn" className="btn btn-info btn-flat">Submit</button>					
					</div>							
				</form>
			</div>
    </div>
      	)}
}
}

export default withRouter(ContactUs);
