import React, { Component } from 'react';
import classes from './OTP.css';
import Profile from '../Profile/Profile';
import image from '../../bg.jpg';
import {
  BrowserRouter as Router,
  Route,
  Link,
  Redirect,
  withRouter
} from "react-router-dom";

var styles = {
color:'violet',
backgroundImage: 'url('+image+')'
};

var ImgPro ={
width: '119px',
height: '119px',
margin:'0 auto'
}

class OTP extends Component {

  constructor(props){
    super(props);
    this.state = {
      email_phone: sessionStorage.getItem('email_phone'),
      password: sessionStorage.getItem('password'),
      otp_code: '',
      isLoaded : false,
    }
  }

  
 handleSubmit(event){
    event.preventDefault();
    const data = new FormData(event.target);
    console.log(data);
   fetch('http://codelive.info/pbg_golf/api/otp_check_post', {
      method: 'POST',
      body: data
    })
   .then(res => res.json())
    .then(json => {
      console.log(json);
      if(json.replyStatus === 'success'){
        sessionStorage.setItem('userId', json.data.id);
        sessionStorage.setItem('userName', json.data.fname+" "+json.data.lname);
        sessionStorage.setItem('isLogin',true);
        window.location = '/Profile';
    }else if(json.replyStatus === 'otp_auth'){
          window.location = '/OTP';
        }
      else{
          alert(json.replyStatus);
        }
     })
    .catch((error)=>{
      console.error(error);
    })
  }
  render() {
    
    var { isLoaded, items } = this.state;

    if (!isLoaded) {
      return (
      <div className={classes.OTP} style={styles}>
      <div className={classes.LoginBox}>
      <div className={classes.LoginBoxBody}>
  <div className="login-logo login_b hide">
      <a href="http://codelive.info/pbg_golf/"><img src="http://codelive.info/pbg_golf/admin_assets/img/logo/1539007624.png" alt="" /></a>
  </div>
    <img src="http://codelive.info/pbg_golf/uploads/SMS-512.png" className="img-responsive otp_box" style={ImgPro}  alt="no"/>
  
  
  <p className={classes.LoginBoxMsg, classes.LoginB, classes.Hide}>Sign in to start your session</p>
  <p className={classes.LoginBoxMsg}>An OTP has been sent to your Email. Please enter the OTP below for Successful Login</p>
  <div className={classes.ErrorHolder}>
    <div id={classes.NewErrorDiv} className={classes.List3, classes.Hide}>
      <h6>Instructions</h6>
      <ul className={classes.ListUnstyled}>
        
      </ul>
    </div>
  </div>
  <div className="login_b hide">
    <form id="loginForm">
      <div className="form-group has-feedback">
        <input type="text" name="email_phone" id="email_phone" className="form-control sty1" placeholder="Email/Phone" />
      </div>
      <div className="form-group has-feedback">
        <input type="password" name="password" id="password" className="form-control sty1" placeholder="Password" />
      </div>
      <div>
        <div className="col-xs-8">
          <div className="checkbox icheck">
            <label>
              <input type="checkbox" />
              Remember Me </label>
            <a href="http://codelive.info/pbg_golf/user/forgot_password" className="pull-right"><i className="fa fa-lock"></i> Forgot Password?</a> </div>
        </div>
        
        <div className="col-xs-4 m-t-1">
          <button type="submit" id="sub_btn" className="btn btn-primary btn-block btn-flat">Wait..</button>
        </div>
         
      </div>
    </form>
  
    <div className="social-auth-links text-center ">
      <p>- OR -</p>
      <a href="https://www.facebook.com/v2.2/dialog/oauth?client_id=733595310323172&amp;state=49341fdefd3e6aa48f9730095e7bb7ae&amp;response_type=code&amp;sdk=php-sdk-5.6.3&amp;redirect_uri=https%3A%2F%2Fcodelive.info%2Fpbg_golf%2Ffacebook%2Ffb_callback&amp;scope=email" className="btn btn-block btn-social btn-facebook btn-flat"><i className="fa fa-facebook"></i> Sign in using
      Facebook</a> <a href="https://accounts.google.com/o/oauth2/auth?response_type=code&amp;redirect_uri=http%3A%2F%2Fcodelive.info%2Fpbg_golf%2Fgoogle&amp;client_id=371434461276-84b6r0afrlvas5prndbh35f6f27m763i.apps.googleusercontent.com&amp;scope=https%3A%2F%2Fwww.googleapis.com%2Fauth%2Fplus.login+https%3A%2F%2Fwww.googleapis.com%2Fauth%2Fplus.me+https%3A%2F%2Fwww.googleapis.com%2Fauth%2Fuserinfo.email&amp;access_type=online&amp;approval_prompt=auto" className="btn btn-block btn-social btn-google btn-flat"><i className="fa fa-google-plus"></i> Sign in using
      Google+</a>
   </div>
   
    
    <div className="m-t-2 ">Don't have an account? <a href="http://codelive.info/pbg_golf/user_signup" className="text-center">Sign Up</a></div>
  </div>
  <div className="otp_box">
  <form id="otpForm" onSubmit={this.handleSubmit}>
      <div className="form-group has-feedback">
    
        <input type="text" name="otp_code" className="form-control sty1" placeholder="Enter OTP" />
      </div>
    <input type="hidden" id="otp_email" name="email_phone" value={this.state.email_phone} />
    <input type="hidden" id="otp_password" name="password" value={this.state.password} />
      <div>
        
        
        <div className="col-xs-12 m-t-1">
          <button type="submit" id="sub_button" className="btn btn-primary btn-block btn-flat">Submit</button>
        </div>
        
      </div>
    {/*<div className="m-t-2 form-inline">
    <label className="mar-right-5">Skip OTP for?</label> 
    <select className="form-control" name="otp_disable_duration">
      <option value="">Select Option</option>
      <option value="3">3 Months</option>
      <option value="6">6 Months</option>
    </select>
    </div>*/}
    <div className="m-t-2 col-xs-8">
          <div className="checkbox icheck">
            <label>
              <input type="checkbox" name="otp_disable_duration" value="30" />
              Trust this device for 30 days </label>
             </div>
        </div>
    </form> 
  </div>
  
  
  </div>
</div>
</div>
    );

    }
  }
}

export default withRouter(OTP);
