import React from 'react';
import classes from './HowItsWork.css';
import { BrowserRouter as Router, Route, Link } from "react-router-dom";
const HowItsWork = (props) =>{
	return(
			<div className={classes.HowItsWork}>
				<section className={classes.SliderMain, classes.HeaderImgBanner}>
	                <div className={classes.BannerMain}>
	                   	<div className={classes.BsSliderOverlay}></div>
	                  	<img src="images/slider1.jpg" alt="Slider1" className="slide-image" />
	                    <div className="container">
			                <div className="row">
			                  <div className={classes.SlideText}>
			                    <h1 data-animation="animated zoomInRight">How it's Work</h1>
			                  </div>
			                </div>
	                	</div>  
	              	</div>
          		</section>
<section className={classes.AboutMidSection}>
<Router>
<div>
			<div className="container">
			    <div className="row">
			        <div className="col-sm-3">
					    <ul className="nav nav-pills nav-stacked" id={classes.AdminMenu}>
			                <li className={classes.Active}><Link to="/"><i className="fa fa-info-circle fa-fw"></i> About Us</Link></li>
			                <li><Link to="/FAQ"><i className="fa fa-question-circle fa-fw"></i> FAQ</Link></li>
			                <li><Link to="/Partners"><i className="far fa-handshake fa-fw"></i> Partners</Link></li>
			                <li><Link to="/Terms"><i className="fa fa-gavel fa-fw"></i> Terms</Link></li>
			                <li><Link to="/PrivacyPolicy"><i className="fas fa-user-shield fa-fw"></i> Privacy Policy</Link></li>
			                <li><Link to="/Support"><i className="fa fa-life-ring fa-fw"></i> Support</Link></li>
			            </ul>       
			        </div>
			        <div className="col-sm-9 well admin-content">
						   <Route exact path="/" component={about} />
						   <Route exact path="/FAQ" component={FAQ} />
						   <Route exact path="/Partners" component={Partners} />
						   <Route exact path="/Terms" component={Terms} />
						   <Route exact path="/PrivacyPolicy" component={PrivacyPolicy} />
						   <Route exact path="/Support" component={Support} />
					</div>
				</div>
			</div>		
</div>
</Router>
</section>
			</div>
		);
};
function about() {
  return (
    <div>
     <p>ParBirdieGolf.com offers quality promotions for public courses at the lowest prices available.&nbsp;</p>
						<p>Our promotions create win/win opportunities for Golfers, Course Operators and</p>
						<p>Manufacturers.&nbsp; Doing so enhances everyones experience, which is great for Golf.</p>
						<p><strong>Our </strong><strong>T</strong><strong>wo Primary Goals:</strong></p>
						<ul>
							<li>Provide courses that best serve the golfer</li>
							<li>Provide the best promotions for all involved</li>
						</ul>    
    </div>
  );
}
