import React, { Component } from 'react';
import classes from './CreateAPackage.css';

class CreateAPackage extends Component {

  constructor(props){
    super(props);
    this.state = {
      items : [],
      isLoaded : false,
    }
  }

  componentDidMount(){
    fetch('http://codelive.info/pbg_golf/api/golf_courses_post')
    .then(res => res.json())
    .then(json => {
      this.setState({
        isLoaded : true,
        items : json,
      })
    });
  }
  render() {
    
    var { isLoaded, items } = this.state;

    if (!isLoaded) {
      return <div> Loading....</div>;
    }
    else{
          return (
          <div className={classes.CreateAPackage}>
           <div>
            {items.data.map(item =>(
             <div className="col-md-4">
        <div className="thumbnail">
          <a href="http://codelive.info/pbg_golf/course/1">
          <img src={item.featured_image_path} alt="Spring Valley"  />
          <div className="caption">
            <p className="text-center">{item.course_name}</p>
          </div>
          </a>
        </div>
      </div>
              ))};
          </div>
          </div>
        );
    }
  }
}

export default CreateAPackage;
