import React, { Component } from 'react';
import classes from './Verification.css';
import image from '../../bg.jpg';
import $ from 'jquery';
import Notifications, {notify} from 'react-notify-toast';
import {
  BrowserRouter as Router,
  Route,
  Link,
  Redirect,
  withRouter
} from "react-router-dom";

var styles = {
color:'violet',
backgroundImage: 'url('+image+')'
};

class Verification extends Component {

  constructor(props){
     super(props);
    this.state = {
      isLoaded: false,
      verif_pass: this.props.match.params.verif_hash,
    }
  }

  
  componentDidMount(){
   fetch('http://codelive.info/pbg_golf/api/verify_email_signup', {
      method: 'POST',
      headers: {
              //"Content-Type": "application/json",
               "Content-Type": "application/x-www-form-urlencoded",
      },
      body: "verif_pass="+this.state.verif_pass,
    })
   .then(res => res.json())
    .then(json => {
      $(".list-unstyled").html("");
      if(json.replyStatus === 'success'){
       $('.reply_msg').html(json.replyMsg);
       $('.reply_msg').removeClass('hide');
        $('.btn_verify').removeClass('hide');
    }else{
          var error = JSON.parse(json.replyMsg);
          $.each(error, function(k, v) { 
          var er = v; 
          $(".list-unstyled").append("<li>"+er+"</li>");
          $("#new_error_div").removeClass('hide');
          $("#sub_button").html('Login'); 
        });
        }
     })
    .catch((error)=>{
      console.error(error);
    })
  }
  render() {
    
    var { isLoaded } = this.state;

   
      return(
      <div className={classes.Verification} style={styles}>
      <Route><div>
      <div className="row m-t-8">
  <div className="col-lg-12 col-md-12 col-sm-12 col-xs-12 text-center">
    <div className={classes.LogoVerifyEmailMsg}>
      <a href="javascirpt:void(0);" ><img className={classes.ImgPosition} src="http://codelive.info/pbg_golf/front_assets/images/logo.png" alt="no_image" /></a>
      
      <p className="reply_msg hide"></p>
      <div className="btn_verify hide">
        <Link to='/login'>Click Here For Go to login page &gt;&gt;</Link>
      </div>
    </div>
    
  </div>
</div>
</div></Route>
</div>
    );
    
  }
}

export default withRouter(Verification);
