import React, { Component } from 'react';
import classes from './PlaceOrderReply.css';
import $ from 'jquery';
import {
  BrowserRouter as Router,
  Route,
  Link,
  Redirect,
  withRouter
} from "react-router-dom";
var styles ={
	margin: '25px 0px',
}
var styles2 ={
	width: '100%',
}
class PlaceOrderReply extends Component {


  render() {
   	return (
   			<div className={classes.PlaceOrderReply}>
   			<div class="col-xs-12" style={styles}>
        	    <div class="form-wrap">
				<div class="text-center" style={styles2}><img class="margin_auto" src="http://codelive.info/pbg_golf/front_assets/images/success.png" width="100px" /></div>
                <h1 class="text-center">Order Successfully Placed</h1>
                    
                  <h4 class="text-center">{sessionStorage.getItem('userName')}</h4>
				  <br/> 
				  <p class="text-center">You Order has been placed successfully.Your Order is waiting for approval.After Approving you get notification Mail.</p> 
                    
        	    </div>
    		</div>
    		</div>
   		);
  }
}

export default withRouter(PlaceOrderReply);
