import React, { Component } from 'react';
import classes from './GolfCourseDetails.css';
import {
  withRouter
} from "react-router-dom";

var imgWidth ={
	    width: '100%',
    height: '300px',
}
var button ={
	background: '#1F497D',
    fontWeight: '600',
    padding: '6px 20px',
    fontSize: '16px',
    width: '150px',
    color: '#fff',
    borderRadius : '4px',
}
var button2 ={
	background: '#1F497D',
    fontWeight: '600',
    padding: '6px 20px',
    fontSize: '16px',
    width: '150px',
    color: '#fff',
    marginLeft : '40px',
    borderRadius : '4px',
}

class GolfCourseDetails extends Component {

  constructor(props){


    super(props);
    this.state = {
      items: [],
      isLoaded : false,
      course_id: this.props.match.params.course_id,
    }
     this.getMyLocation = this.getMyLocation.bind(this)
  }

  componentDidMount(){
  	this.getMyLocation()
	//var data = new FormData();
	
	//data.append("incentive_id", this.state.incentive_id);
	//var data = JSON.stringify(userSchema);
	
	fetch('http://codelive.info/pbg_golf/api/golf_course_detail_post', {
      	method: 'POST',
 		headers: {
	            //"Content-Type": "application/json",
	             "Content-Type": "application/x-www-form-urlencoded",
	  	},
      	body: 'course_id='+ this.state.course_id +'&latitude='+this.state.latitude+'&longitude='+this.state.longitude
  
    })
    .then(res => res.json())
    .then(json => {
      this.setState({
        isLoaded : true,
        items : json.data,
      })
    });
  }

  getMyLocation() {
    //const location = window.navigator && window.navigator.geolocation
    const location = window.navigator.geolocation
     console.log(location); return false;
    if (location) {
      location.getCurrentPosition((position) => {
        this.setState({
          latitude: position.coords.latitude,
          longitude: position.coords.longitude,
        })
      }, (error) => {
        this.setState({ latitude: 'err-latitude', longitude: 'err-longitude' })
      })
    }


  }
  
  render() {
     const { latitude, longitude } = this.state
     
    var { isLoaded, items } = this.state;

    if (isLoaded) {
      return (
      	<div className={classes.GolfCourseDetails}>
      	<section className="golf_courses fix padTB50">
	<div className="container">
		<div className={classes.CourseName}>
			<h1>{items.course_name}</h1>
		</div>
		<div className="col-md-6">
			<div className="course">
				<div className="course_image">
									<img src={items.featured_image_path} alt="no_img" style={imgWidth} />
								</div>
				<div className="course_subheading" style={{padding: '10px 0px'}}>
					<b><p className="text-center">{items.address}<span style={{paddingLeft:'5px'}}>{items.city_id}</span><span style={{paddingLeft:'5px'}}>{items.state_id}</span></p></b>
				</div>
				<div className="course_para">
					<p style={{fontSize: '16px'}}>{items.detail} </p>
				</div>	
				<div className="course_btn text-center" style={{paddingTop : '15px'}}>
					<a href="javascript:void(0);" onClick="select_course(1);" className="btn" style={button}>
						Select
					</a>
					<a href="javascript:void(0);" onClick="window.history.back();" className="btn marL40" style={button2}>
						Back
					</a>					
				</div>
			</div>
		</div>
		<div className="col-md-6">
			<div className={classes.CourseTable}>
				<div className="table-responsive">          
				  <table className="table text-center">
					<thead>
					  <tr>
						<th>Tee</th>
						<th>Par</th>
						<th>Length</th>
						<th>Rating</th>
						<th>Slope</th>
					  </tr>
					</thead>
					<tbody>
										  <tr>
						<td>Gold</td>
						<td>72</td>
						<td>6422</td>
						<td>71.2</td>
						<td>128</td>
					  </tr>	
											  <tr>
						<td>Blue</td>
						<td>72</td>
						<td>5877</td>
						<td>68.8</td>
						<td>118</td>
					  </tr>	
											</tbody>
				  </table>
				</div>
			</div>
			<div className={classes.CourseTable}>
				<div className="table-responsive">          
				  <table className="table text-center">
					<thead>
					  <tr>
						<th>Policies</th>
						<th>Rentals/Services</th>
						<th>Practice/Instruction</th>
					  </tr>
					</thead>
					<tbody>
					  <tr>
						<td>Metal spikes: {items.metal_spikes === '0'? ' no': ' yes'}</td>
						<td>Carts: {items.carts === '0'? ' no': ' yes'}</td>
						<td>Driving Range: Yes</td>
					  </tr>	
					  <tr>
						<td>Dress Code: {items.dress_code}</td>
						<td>Clubs: {items.clubs === '0'? ' no': ' yes'}</td>
						<td>Pitching/chipping: Yes</td>
					  </tr>	
					  <tr>
						<td>&nbsp;</td>
						<td>Pro Shop: {items.metal_spikes === '0'? ' no': ' yes'}</td>
						<td>Putting green: {items.putting_green === '0'? ' no': ' yes'}</td>
					  </tr>	
					  <tr>
						<td>&nbsp;</td>
						<td>&nbsp;</td>
						<td>Practice bunker: {items.practice_bunker === '0' ? ' no': ' yes'}</td>
					  </tr>						  
					</tbody>
				  </table>
				</div>
			</div>
		</div>		
	</div>
</section>
</div>
      	);
  }
  else{
          return (
           <div>Loading...</div>
        );
    }
}
}

export default withRouter(GolfCourseDetails);
