import React, { Component } from 'react';
import classes from './Register.css';
import image from '../../bg.jpg';
import $ from 'jquery';
import Notifications, {notify} from 'react-notify-toast';
import {
  BrowserRouter as Router,
  Route,
  Link,
  Redirect,
  withRouter
} from "react-router-dom";

var styles = {
color:'violet',
backgroundImage: 'url('+image+')'
};
var styles2 = {
	backgroundColor: '#DD4B39',
	color: 'white',
	borderRadius : '5px',
	padding : '5px',
}
var fontSize = {
	fontSize: '18px',
}

class Register extends Component {

  constructor(props){
    super(props);
    this.state = {
      fname: '',
      lname:'',
      email: '',
      phone: '',
      password: '',
      cpassword: '',
      isLoaded : false,
    }
  }

  
  handleSubmit(event){
  	event.preventDefault();
  	const data = new FormData(event.target);
    console.log(data);
   fetch('http://codelive.info/pbg_golf/api/signup_post', {
      method: 'POST',
      body: data
    })
   .then(res => res.json())
    .then(json => {
    	$(".list-unstyled").html("");
      if(json.replyStatus === 'success'){
      	notify.show(json.replyMsg,"success", 5000, "green");
      	$("#addForm")[0].reset();
		}else if(json.replyStatus === 'otp_auth'){
      		window.location = '/OTP';
      	}
      else{
      		var error = JSON.parse(json.replyMsg);
      		$.each(error, function(k, v) { 
					var er = v; 
					$(".list-unstyled").append("<li>"+er+"</li>");
					$("#new_error_div").removeClass('hide');
					$("#sub_button").html('Login'); 
				});
      	}
   	 })
    .catch((error)=>{
    	console.error(error);
    })
  }

  handlePasswordTest(e){
  	if(e.target.value !== ''){
  	fetch('http://codelive.info/pbg_golf/api/password_test', {
      method: 'POST',
      headers: {
	            //"Content-Type": "application/json",
	             "Content-Type": "application/x-www-form-urlencoded",
	  	},
      	body:  "password="+e.target.value,
    })
   .then(res => res.json())
    .then(json => {
    	$(".list-unstyled").html("");
      if(json.replyStatus === 'success'){
      	$(".list-unstyled").html(" ");
					$("#new_error_div").addClass('hide');
					$("#password").parent().removeClass('has-error');
					$(".progress-bar").attr('aria-valuenow',100);
					$(".progress-bar").attr('class','progress-bar progress-bar-success');
					this.progress_bar();
      	}else if(json.replyStatus === 'error'){
      		$("#password").parent().addClass('has-error');
					$(".list-unstyled").html("<li>"+json.replyMsg+"</li>");
					$("#new_error_div").removeClass('hide');
					//$("#password").parent().find('.popuptext').toggleClass('show');
					if(json.data == 1){
						$(".progress-bar").attr('aria-valuenow',55);
						$(".progress-bar").attr('class','progress-bar progress-bar-warning');
					}else{
						$(".progress-bar").attr('aria-valuenow',30);
						$(".progress-bar").attr('class','progress-bar progress-bar-danger');
					}
					
					this.progress_bar();
      	}else{
      		console.log(json);
      	}
   	 })
    .catch((error)=>{
    	console.error(error);
    })
	}else{
		$(".list-unstyled").html(" ");
		$("#new_error_div").addClass('hide');
		$("#password_error").parent().removeClass('has-error');
		$(".progress-bar").attr('aria-valuenow',0);
		this.progress_bar();
	}
  }
  progress_bar(){
		 $('.progress .progress-bar').css("width",
                function() {
                    return $(this).attr("aria-valuenow") + "%";
                }
        );
	}

  render() {
    
    var { isLoaded, items } = this.state;

    if (!isLoaded) {
      return (
			<div className={classes.Register} style={styles}>
			<Notifications options={{zIndex: 200, top: '100px'}} />
			<div className={classes.RegisterBox}>
				  <div className={classes.RegisterBoxBody}>
				  <div className={classes.RegisterLogo}>
				      <a href="http://codelive.info/pbg_golf/"><img src="http://codelive.info/pbg_golf/admin_assets/img/logo/1539007624.png" alt="" /></a>
				  </div>
				  
				    <p className={classes.RegisterBoxMsg}>Sign Up</p>
					<div className={classes.ErrorHolder}>
						<div id="new_error_div" className="list3 hide" style={styles2}>
							<h6 style={fontSize}>Instructions</h6>
							<ul className="list-unstyled">
								
							</ul>
						</div>
					</div>
					
					<div class="clearfix"></div>
				    <form id="addForm" onSubmit={this.handleSubmit}>
						<div class="row">
							<div class="col-md-6">
							  <div class="form-group has-feedback">
								<input type="text" name="fname" class="form-control" id={classes.Sty1} placeholder="* First Name" tabindex="0"/>
							  </div>
							  <div class="form-group has-feedback">
								<input type="text" name="phone" class="form-control" id={classes.Sty1} placeholder="* Phone" tabindex="2" />
							  </div>
							  <div class="form-group has-feedback popup pos_rel">
								<input type="password" name="password" id="password" class="form-control" id={classes.Sty1} placeholder="* Password" tabindex="4" onChange={((e) => this.handlePasswordTest(e))}/>
							  </div>		  
							</div>
							<div class="col-md-6">
							  <div class="form-group has-feedback">
								<input type="text" name="lname" class="form-control" id={classes.Sty1} placeholder="* Last Name" tabindex="1" />
							  </div>  	
							  <div class="form-group has-feedback">
								<input type="email" name="email" class="form-control" id={classes.Sty1} placeholder="* Email" tabindex="3" />
							  </div>
							  <div class="form-group has-feedback">
								<input type="password" name="cpassword" class="form-control" id={classes.Sty1} placeholder="* Confirm Password" tabindex="5" />
							  </div>		  
							</div> 
							
						</div> 
						<div class="row">
							<div class="col-md-2">
								<p>Strength:</p>
							</div>
							<div class="col-lg-10">
								<div class="progress skill-bar">
									<div class="progress-bar" role="progressbar" aria-valuemin="0" aria-valuemax="100">
										<span class="skill"></span>
									</div>
								</div>
							</div>	
						</div>
						
				      <div>

				 
				        <div class="col-xs-12 m-t-1">
				          <button type="submit" id="sub_btn" class="btn btn-primary btn-block btn-flat">Sign Up</button>
				        </div>
						
				       
				      </div>
				    </form>
				    <div class="social-auth-links text-center ">
				      <p>- OR -</p>
				      <a href="#" class="btn btn-block btn-social btn-facebook btn-flat" id={classes.Facebook}><i class="fab fa-facebook"></i> Sign Up using
				      Facebook</a> <a href="#" class="btn btn-block btn-social btn-google btn-flat" id={classes.Google}><i class="fab fa-google-plus"></i> Sign Up using
				      Google+</a> </div>
				   
				    
				    <div className={classes.Mt2} >Already have an account? <Link to="/Login" class="text-center">Sign In</Link></div>
				  </div>
</div>
</div>
		);
    }
  }
}

export default withRouter(Register);
